.basket {
    padding-top: 24px;
    padding-bottom: 80px;

    @include tablet {
        padding-top: 0px;
        padding-bottom: 120px;
    }

    &__header {
        margin-bottom: 29;
        flex-direction: column;

        .btn--transparent {
            max-width: none;
        }

        @include phone {
            margin-bottom: 32px;
            flex-direction: row;

            .btn--transparent {
                max-width: 127px;
            }
        }

        @include tablet {
            .btn--transparent {
                max-width: 147px;
            }
        }
    }

    &__wrapper {
        border: 1px solid var(--gray-light-color);
        border-radius: 8px;
        margin-bottom: 40px;

        @include tablet {
            margin-bottom: 77px;
        }
    }

    &__mobile-text {
        font-weight: 400;
        font-size: 14px;
        line-height: 18px;
        color: #96ACB3 !important;
        margin-right: 5px;
        padding: 0 !important;

        @include tablet {
            display: none;
        }
    }

    &__about {
       
        @include desktop {
            display: flex;
            justify-content: space-between;
        }
    }

    &__points {
        margin-bottom: 40px;

        @include desktop {
            margin-bottom: 0px;
        }
    }
}

.table {
    border: 1px solid var(--gray-light-color);
    border-radius: 8px;

    // @include tablet {
    //     display: grid;
    // }

    &-head {
        display: none;
        grid-template-columns: repeat(12,1fr);
        align-items: center;
        border-bottom: 1px solid var(--gray-light-color);
        padding: 12px 0;

        @include tablet {
            display: grid;
        }

        &__item {
            text-align: center;
            font-weight: 400;
            font-size: 12px;
            line-height: 18px;
            color: var(--gray-color);
    
            &:nth-child(1) {
                grid-column: 6/8;
                
                @include widescreen {
                    grid-column: 5/7;
                }
            
            }
    
            &:nth-child(2) {
                grid-column: 8/10;
                @include widescreen {
                    grid-column: 7/9;
                }
                
               
            }
    
            &:nth-child(3) {
                grid-column: 9/10;
                display: none;
    
                @include widescreen {
                    display: block;
                }
            
            }
    
            &:nth-child(4) {
                grid-column: 10/11;
    
            }
    
    
            &:nth-child(5) {
                grid-column: 11/13;
            }
        }
    }

    &-info {
        grid-column: 4 span;

        @include widescreen {
            grid-column: 2 span;
        }
    }

    &-item {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        align-items: flex-start;
        padding: 10px 10px;
        position: relative;
    
        &:not(:last-child) {
            border-bottom: 1px solid var(--gray-light-color);
        }

        @include tablet {
            grid-template-columns: repeat(12,1fr);
            align-items: center;
            padding: 10px 0;
        }
    }

    &-name {
        display: block;
        font-weight: 700;
        font-size: 14px;
        line-height: 120%;
        color: var(--black-color);
        margin-bottom: 14px;

        @include phone {
            font-size: 16px;
        }

        @include tablet {
            margin-bottom: 12px;
        }
    }

    &-img {
        grid-column: 4 span;
        height: 230px;

        @include tablet {
            grid-column: 1 span;
            height: 116px;
        }

        @include widescreen {
            grid-column: 2 span;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &-article {
        font-weight: 400;
        font-size: 12px;
        line-height: 18px;
        color: var(--black-color);
        display: none;

        @include phone {
            display: block;
            margin-bottom: 12px;
        }

        @include tablet {
            display: block;
            margin-bottom: 0px;
        }
    }

    &-price {
        text-align: left;
        font-weight: 700;
        font-size: 16px;
        line-height: 100%;
        color: var(--black-color);
        display: none;

        @include tablet {
            display: block;
            grid-column: 2 span;
            text-align: center;
            font-size: 16px;
        }

        @include widescreen {
            font-size: 18px;
        }
    }

    &-counter {
        grid-column: 1/5;
        grid-row: 5/5;
        display: flex;
        align-items: center;
        height: 24px;
        margin-bottom: 20px;

        .counter-wrapper {
            
            .btn--minus, .btn--plus {
                width: 26px;
                height: 24px;
            }

            .counter {
                input {
                    width: 28px;
                    height: 24px;
                    font-size: 16px;
                }
            }
        }

        @include phone {
            height: 36px;

            .counter-wrapper {
                .btn--minus, .btn--plus {
                    width: 39px;
                    height: 34px;
                }

                .counter {
                    input {
                        width: 39px;
                        height: 34px;
                        font-size: 18px;
                    }
                }
            }
        }

        @include tablet {
            grid-column: 2 span;
            grid-row: auto;
            margin-bottom: 0;
        }

        .counter-wrapper {
            border: 1px solid var(--gray-semilight-color);
          

            @include tablet {
                margin: 0 auto;
                max-width: 120px;
            }
        }
    }

    &-maxdiscount {
        grid-column: 1/4;
        text-align: left;
        font-weight: 700;
        font-size: 14px;
        line-height: 100%;
        color: var(--black-color);
        display: flex;
        align-items: center;
        margin-bottom: 8px;

        @include phone {
            font-size: 16px;
        }

        @include tablet {
            grid-column: 1 span;
            text-align: center;
            font-size: 18px;
            display: none;
            margin-bottom: 0;
        }

        @include widescreen {
            display: block;
            
        }

        span {
            padding: 0 5px;
    
            @include widescreen {
                padding: 0;
            }
        }
    }

    &-discount {
        grid-column: 1/5;
        text-align: left;
        font-weight: 700;
        font-size: 16px;
        line-height: 100%;
        color: var(--black-color);
        margin-bottom: 8px;

        @include phone {
            font-size: 16px;
        }

        @include tablet {
            grid-column: 1 span;
            text-align: center;
            font-size: 18px;
            margin-bottom: 0;
        }
    }

    &-amount {
        grid-column: 1/5;
        text-align: left;
        font-weight: 700;
        font-size: 16px;
        line-height: 100%;
        color: var(--black-color);
        margin-bottom: 8px;

        @include phone {
            font-size: 16px;
        }

        @include tablet {
            grid-column: 1 span;
            text-align: center;
            font-size: 18px;
            margin-bottom: 0;
        }
    }

    &-sum {
        grid-column: 1/5;
        grid-row: 6/6;
        text-align: left;
        font-weight: 700;
        font-size: 16px;
        line-height: 100%;
        color: var(--black-color);

        .basket__mobile-text {
            font-weight: 700;
            font-size: 16px;
            line-height: 100%;
            color: #133D4D !important;
        }


        @include phone {
            font-size: 18px;
        }

        @include tablet {
            grid-column: 2 span;
            text-align: center;
            font-weight: 700;
            grid-row: auto;
        }
    }
    
    &-article {
        font-weight: 400;
        font-size: 12px;
        line-height: 18px;
        color: var(--black-color);
        display: none;

        @include phone {
            display: block;
            margin-bottom: 12px;
        }
    }
}

.discount-active {
    span {
        color: var(--green-color);
        padding: 0 5px;

        @include widescreen {
            padding: 0;
        }
    }
}

