.payment-info {
    max-width: 461px;
    padding: 16px 8px;
    background-color: var(--gray4-color);
    border: 1px solid var(--gray-light-color);
    border-radius: 8px;

    .currency {
        display: inline-block;
    }

    .btn--secondary {
        margin-bottom: 16px;
    }

    @include phone {
        padding: 24px 20px;
    }

    @include tablet {
        padding: 32px 45px;
    }

    &__body {
        margin-bottom: 30px;

        @include tablet {
            margin-bottom: 38px;
        }
    }

    &__item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 12px;
    }

    &__title {
        font-weight: 700;
        font-size: 18px;
        line-height: 100%;
        color: var(--black-color);
        margin-bottom: 24px;

        @include tablet {
            font-size: 22px;
            margin-bottom: 31px;
        }

    }

    &__text {
        display: block;
        font-weight: 400;
        font-size: 14px;
        line-height: 100%;
        color: var(--black-color);

        @include phone {
            font-size: 16px;
        }

        @include tablet {
            font-size: 18px;
        }
    }

    &__sum {
        display: block;
        font-weight: 400;
        font-size: 14px;
        line-height: 100%;
        color: var(--black-color);

        @include phone {
            font-size: 16px;
        }

        @include tablet {
            font-size: 18px;
        }
    }

    &__final {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 22px;

        @include tablet {
            margin-bottom: 24px;
        }

        &-text {
            display: block;
            font-weight: 700;
            font-size: 18px;
            line-height: 100%;
            color: var(--black-color);

            @include tablet {
                font-size: 22px;
            }

            .currency {
                position: relative;
                display: inline-block;
                padding-right: 10px;
                
                &::after {
                    content: "\204e";
                    position: absolute;
                    top: -7px;
                    right: 0;
                    z-index: 1;
                    font-weight: 700;
                    font-size: 22px;
                    line-height: 100%;
                    color: var(--black-color);
                }
            }
        }
    }

    &__descr {
        font-weight: 400;
        font-size: 12px;
        line-height: 120%;
        color: var(--gray-color);
    }

}

.article-code {

    display: none;

    @include phone {
        display: inline-block;
    }
}

.basket-head {
    display: none;
    grid-template-columns: repeat(12,1fr);
    align-items: center;
    border-bottom: 1px solid var(--gray-light-color);
    padding: 12px 0;

    @include tablet {
        display: grid;
    }

    &__item {
        text-align: center;
        font-weight: 400;
        font-size: 12px;
        line-height: 18px;
        color: var(--gray-color);

        &:nth-child(1) {
            grid-column: 6/8;
            
            @include widescreen {
                grid-column: 5/7;
            }
        
        }

        &:nth-child(2) {
            grid-column: 8/10;
            @include widescreen {
                grid-column: 7/9;
            }
            
           
        }

        &:nth-child(3) {
            grid-column: 9/10;
            display: none;

            @include widescreen {
                display: block;
            }
        
        }

        &:nth-child(4) {
            grid-column: 10/11;

        }


        &:nth-child(5) {
            grid-column: 11/13;
        }
    }

    &--order {
        .basket-head__item--order {
            &:nth-child(1) {
                grid-column: 6/8;

                @include tablet {
                    grid-column: 3/4;
                }
                
                @include widescreen {
                    grid-column: 3/3;
                }
            
            }
    
            &:nth-child(2) {
                grid-column: 8/10;
                display: none;
                @include widescreen {
                    grid-column: 6/8;
                    display: block;
                }
                
               
            }
    
            &:nth-child(3) {
                grid-column: 8/10;
                display: block;
    
                @include tablet {
                    grid-column: 7/9;
                }

                @include widescreen {
                    grid-column: 8/10;
                }
            
            }
    
            &:nth-child(4) {
                grid-column: 10/11;
                @include tablet {
                    grid-column: 9/11;
                }
               
                @include widescreen {
                    grid-column: 10/11;
                }
            }
    
    
            &:nth-child(5) {
                grid-column: 11/13;
            }
        }
    }
}

.basket-points {
    display: block;
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
    color: var(--black-color);

    @include phone {
        font-size: 16px;
    }

    @include desktop {
        font-size: 18px;
    }

    &:not(:last-child) {
        margin-bottom: 12px;
    }

    span {
        font-weight: 700;
        padding-left: 9px;
    }
}

.basket-input {
    border: 1px solid #D9E4E7;
    border-radius: 4px;
    max-width: 100px;
    display: inline-flex;
    justify-content: center;
    margin-left: 9px;

    input {
        width: 44%;

        &:focus-visible {
            outline: none;
        }
    }
}

.head-mobile {
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #96ACB3 !important;
    margin-right: 5px;
    padding: 0 !important;

    @include tablet {
        display: none;
    }
}

.basket-empty {
    padding-top: 24px;
    padding-bottom: 120px;

    @include tablet {
        padding-top: 32px;
        padding-bottom: 160px;
    }

    &__text {
        font-weight: 400;
        font-size: 14px;
        line-height: 115%;
        color: var(--black-color);
        margin-bottom: 24px;

        @include phone {
            font-size: 16px;
        }

        @include tablet {
            font-size: 18px;
            margin-bottom: 36px;
        }
    }

    .btn--secondary {
        max-width: 145px;
    }
}