* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}


html {
  box-sizing: border-box;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

.page {
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  height: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  height: 100%;
  font-size: 10px;

}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; //     position: sticky -   
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

// .is-hidden {
//   display: none !important; /* stylelint-disable-line declaration-no-important */
// }

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.scroll-wrap::-webkit-scrollbar {
  width: 5px;
  background-color: #F6F8FC;
}

.scroll-wrap::-webkit-scrollbar-thumb {
  background: #D9E4E7;
  border-radius: 12px;
}

.scroll {
  overflow: hidden;
  position: relative;
  height: 100vh;
  width: 100vw;
}

.form {
  
  &__group {
    display: flex;
    flex-direction: column;
    margin-bottom: 8px;
    position: relative;
  }

  &__name {
    text-align: left;
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
    color: #96ACB3;
    margin-bottom: 4px;
  }

  &__input {
    border: 1px solid #D9E4E7;
    border-radius: 2px;
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
    color: var(--black-color);
    padding: 8px 8px 8px 8px;
    height: 34px;

    @include tablet{
      padding: 14px 14px 14px 14px;
      height: 42px;
    }

    &:focus-visible {
      outline: 1px solid #133D4D;
    }

    &::placeholder {
      color: var(--gray-semilight-color);
    }
  }

  &__textarea {
    border: 1px solid #D9E4E7;
    border-radius: 2px;
    resize: none;
    padding: 8px 12px 8px 12px;
    font-weight: 400;
    font-size: 14px;
    line-height: 120%;
    color: var(--black-color);

    &::placeholder {
      color: var(--gray-semilight-color);
    }

    &:focus-visible {
      outline: 1px solid var(--black-color);
    }
  }

  &__error-text {
    opacity: 0;
  }

  &__checkbox {
    margin-bottom: 16px;
  }

  &__search {
    width: 100%;
    position: relative;
    &-input {
      width: 100%;
      background-color: var(--gray4-color);
      border: 1px solid #F3F3F3;
      border-top-right-radius: 2px;
      border-bottom-right-radius: 2px;
      height: 38px;
      font-weight: 400;
      font-size: 12px;
      line-height: 18px;
      padding: 5px 38px 8px 8px;
      color: var(--black-color);

      &:focus-visible {
        outline: 1px solid var(--black-color);
      }

      @include tablet {
        padding: 5px 38px 8px 10px;
        border-top-right-radius: 6px;
        border-bottom-right-radius: 6px;
      }

      @include desktop {
        font-size: 14px;
        line-height: 20px;
        padding: 8px 38px 10px 16px;
      }

    }

    &-btn {
      position: absolute;
      top: 0;
      right: 0px;
      z-index: 1;
      width: 43px;
      height: 38px;
    }
  }

  &__call {
    .btn--secondary {
      padding: 11px 0;
    }

    .form__checkbox {
      margin-bottom: 32px;
    }
  }

  &__search-filters {
    position: relative;
    width: 100%;
    margin-bottom: 8px;

    .form__input {
      width: 100%;
      border-radius: 4px;
      border: 1px solid #F5F5F5;
      font-weight: 400;
      font-size: 14px;
      line-height: 21px;
      color: var(--black-color);
      padding: 6px 32px 6px 8px;

      &::placeholder {
        color: var(--black-color);
      }

      &:focus-visible {
        outline: transparent;
      }
    }

    .form__btn {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 0px;
      z-index: 1;
      width: 35px;
      height: 35px;

      img {
        width: 14px;
        height: 14px;
      }
    }
  }

  &__questions {
    .form__textarea {
      height: 114px;
    }

    .form__checkbox {
      margin-bottom: 24px;
    }

    .btn--secondary {
      max-width: 318px;
    }
  }

  &__login {
    margin-bottom: 16px;

    @include tablet {
      margin-bottom: 24px;
    }
  }

  &__registration {
    margin-bottom: 16px;

    @include tablet {
      margin-bottom: 24px;
    }
  }

  &__restore {
    margin-bottom: 16px;

    @include tablet {
      margin-bottom: 24px;
    }
  }

  &__user {
    &:first-child {
      margin-bottom: 80px;

      @include widescreen{
        margin-bottom: 120px;
      }
    }

    .form__body {
      margin-bottom: 16px;

      @include tablet{
        margin-bottom: 30px;
      }
    }
    .form__group {
      &:not(:last-child) {
        margin-bottom: 22px;
      }
    }

    .form__input {
      font-weight: 400;
      font-size: 16px;
      line-height: 100%;
      padding: 9px 35px 8px 12px;
    }

    .form__name {
      color: var(--black-color);
    }

    .form__checkbox {
      margin-bottom: 0;
    }

    .custom-checkbox__text {
      color: var(--black-color);
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
    }

    .custom-checkbox__content::before {
      border: 1px solid var(--black-color);
    }
    .btn--secondary {

      padding: 11px 10px;

      @include tablet{
        max-width: 218px;
      }
    }

    .form__btn-eye {
      position: absolute;
      top: 19px;
      right: 0;
      z-index: 1;
      width: 40px;
      height: 40px;

      img {
        width: 22px;
        height: 15px;
      }
    }
  }

  &__promo {
    margin-top: 28px;
    .form__input {
      background: #FFFFFF;
      border: 1px solid #D9E4E7;
      border-radius: 2px;
      width: 100%;
      font-weight: 400;
      font-size: 14px;
      line-height: 100%;
      text-transform: uppercase;
      letter-spacing: 0.2em;
      color: var(--black-color);
      padding: 8px 8px 8px 8px;
    }

    .form__btn {
      width: 100%;
      background: #B5C9CE;
      border-radius: 4px;
      font-weight: 700;
      font-size: 14px;
      line-height: 100%;
      color: #FFFFFF;
      padding: 11px 10px;

      @include tablet{
        font-size: 16px;
      }
    }
  }
}

.account-link {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: var(--gray-color);
  position: relative;
  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 1px;
    background-color: var(--gray-color);
  }
}

.section-header {
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  @include tablet{
    margin-bottom: 32px;
    align-items: center;
  }

  @include widescreen{
    margin-bottom: 40px;
   
  }

  &__wrap {
    display: flex;
   
    flex-direction: column;
    align-items: flex-start;

    @include tablet{
      flex-direction: row;
      align-items: baseline;
    }
  }
}

.section-title {
  font-weight: 700;
  font-size: 22px;
  line-height: 120%;
  color: var(--black-color);
  margin-bottom: 2px;

  @include tablet{
    font-size: 32px;
    line-height: 120%;
    margin-bottom: 0;
  }

}

.section-goods {
  display: block;
  font-weight: 400;
  font-size: 18px;
  line-height: 180%;
  text-transform: lowercase;
  color: var(--gray-color);
 

  @include tablet {
    margin-left: 32px;
  }
}

.section-link {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: var(--gray-color);
  position: relative;
  display: block;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 1px;
    background-color: var(--gray-color);

    @include tablet{
     display: none;
    }
  }

  @include tablet{
    font-size: 18px;
    line-height: 180%;
    margin-left: 32px;
  }
}

.underline-link {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: var(--gray-color);
  position: relative;
  &::before {
    content: "";
    position: absolute;
    bottom: 3px;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 1px;
    background-color: var(--gray-color);
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  width: 100%;
  font-weight: 700;

  font-size: 14px;
  line-height: 100%;
  color: var(--white-color);
  position: relative;
  transition: var(--base-transition);

  @include tablet {
    font-size: 16px;
  }

  &--primary {
    background-color: var(--orange-color);
    padding: 6px 40px;

    &:hover {
      background-color: var(--orange-hover-color);
    }

    &:active {
      background-color: var(--orange-active-color);
    }
  }

  &--secondary {
    background-color: var(--green-color);
    padding: 11px 35px;
    

    &:hover {
      background-color: var(--green-hover-color);
    }

    &:active {
      background-color: var(--green-active-color);
    }
    
  }

  &--thirdly {
    background-color: var(--gray-color);
    padding: 11px 0px;
    font-size: 12px;

    @include phone {
      font-size: 13px;
    }

    @include tablet {
      font-size: 16px;
      padding: 11px 0px;
    }

    &:hover {
      background-color: var(--gray-semi-color);
    }

    &:active {
      background-color: var(--gray-color);
    }
  }

  &--transparent {
    border: 1px solid var(--gray-color);
    border-radius: 4px;
    font-weight: 700;
    font-size: 14px;
    line-height: 100%;
    color: var(--gray-color);
    max-width: 147px;
    padding: 10px 8px;
  }

  &--delete {
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: var(--gray-color);
    position: absolute;
    bottom: 10px;
    right: 24px;
    z-index: 1;
    width: 58px;
    text-decoration: underline;
    justify-content: flex-start;

    background-image: url("../img/delete.svg");
    background-size: 12px 13px;
    background-position: right center;
    background-repeat: no-repeat;
  }

  &--category {
    background-color: var( --gray4-color);
    border-radius: 2px;
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: var(--black-color);
    padding: 5px 12px;
    white-space: nowrap;
  }

  &--white {
    background-color: var(--white-color);
    border: 1px solid var(--black-color);
    border-radius: 4px;
    font-weight: 700;
    font-size: 16px;
    line-height: 100%;
    color: var(--black-color);
    padding: 14px 0px;
  }

  &--reset {
    font-weight: 700;
    font-size: 16px;
    line-height: 100%;
    color: #B5C9CE;
    background: #F6F8FC;
    padding: 11px 35px;
  }

  &--underline {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--gray-color);
    
    &::before {
      content: "";
      position: absolute;
      bottom: 5px;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 1px;
      background-color: var(--gray-color);
    }
  }

  &--border {
    background-color: var(--white-color);
    border: 1px solid var(--green-color);
    padding: 10px 35px;
    color: var(--green-color);

    &:hover {
      background-color: var(--green-hover-color);
      color: var(--white-color);
    }

    &:active {
      background-color: var(--green-active-color);
    }
  }


  &--minus,
  &--plus { 
    width: 39px;
    height: 34px;
    background-color: white; 
    flex-shrink: 0;
    position: relative;
  }
}

.swiper-button {
  width: 30px;
  height: 30px;
  background-color: var(--gray4-color);
  border-radius: 6px;
  cursor: pointer;
}

.swiper-buttons {
  display: flex;
  &__prev {
      background-image: url("../img/arrow-black.svg");
      background-repeat: no-repeat;
      background-size: 12px 12px;
      background-position: center center;
      transform: rotate(180deg);
  }

  &__next {
      background-image: url("../img/arrow-black.svg");
      background-repeat: no-repeat;
      background-size: 12px 12px;
      background-position: center center;
      margin-left: 12px;
  }
}

._scroll-disabled {
  overflow: hidden;
  width: 100vw;
  height: 100vh;
}


.custom-checkbox {
  @include custom-checkbox;
}

.tooltip {
  position: relative;
  margin-left: 10px;
}

.tooltip__btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  border-radius: 100%;
  background: transparent;
}

.tooltip__txt {
  position: absolute;
  left: -7px;
  top: calc(50% + 14px);
  z-index: 3;
  padding: 8px 14px;
  background: #FFFFFF;
  border: 1px solid #D9E4E7;
  border-radius: 4px;
  width: max-content;
  max-width: 211px;
  font-weight: 400;
  font-size: 12px;
  line-height: 110%;
  color: #133D4D;
  background: #fff;
  opacity: 0;
  visibility: hidden;
  transform: translateY(10px);
  text-transform: none;
  transition: opacity .3s ease-in-out,transform .3s ease-in-out, visibility .3s ease-in-out;
}



.tooltip__btn:hover + .tooltip__txt {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.tooltip__btn:focus + .tooltip__txt {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
     display: none; 
    -webkit-appearance: none;
    margin: 0; 
}

.product-admission {
  border: 1px solid #F5F5F5;
  border-radius: 8px;
  padding: 10px 10px;
  display: flex;
  align-items: center;

  &__img {
    height: 64px;
    flex: 1 0 20%;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__name {
    display: block;
    font-weight: 700;
    font-size: 13px;
    line-height: 120%;
    color: var(--black-color);
    padding-left: 10px;
  }
}