.select {
    position: relative;
    width: 205px;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: var(--black-color);
    display: flex;
    align-items: center;

    &__head {
      display: flex;
      align-items: center;
      background-color: var(--white-color);
      border-radius: 10px;
      padding: 6px 0px 6px 8px;
      cursor: pointer;
      width: 100%;
      height: 44px;
      position: relative;
      border: 1px solid #D9E4E7;
      border-radius: 4px;
  
      &:after {
        content: "";
        position: absolute;
        z-index: 1;
        top: 50%;
        right: 1.2rem;
        width: 14px;
        height: 10px;
        transform: translateY(-50%);
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
        transition: all .3s ease;
        background-image: url("../img/arrow-down.svg");
      }
  
      &.active {
        &::after {
          transform: translateY(-50%) rotate(180deg);
        }
      }
    }
  
    &__text {
      font-family: var(--font-family);
    }
  
    &__list {
      display: flex;
      flex-direction: column;
      background-color: var(--white-color);
      border: 1px solid #D9E4E7;
      border-radius: 0px 0px 4px 4px;      
      position: absolute;
      z-index: 2;
      top: 43px;
      left: 0;
      right: 0;
      opacity: 0;
      transform: scaleY(0);
      transform-origin: 0 0;
      transition: all .3s ease;
      padding: 0;
  
      &.active {
        opacity: 1;
        transform: scaleY(1);
      
      }
    }
  
    &__item {
      min-height: 40px;
      padding: 6px 0px 6px 8px;
      display: flex;
      align-items: center;
      cursor: pointer;
  
      &.active {
        // background-color: rgb(246, 246, 246);
        color: var(--green-color);
      }

      &:hover {
        background-color: var(--gray4-color);
      }
    }
  
    &__img {
      width: 20px;
      height: 20px;
      border-radius: 50%;
      overflow: hidden;
      margin-right: 10px;
  
      img {
        height: 100%;
        object-fit: cover;
      }
    }
}
 
.select-wrapper {
  display: flex;
  align-items: center;
}

.select-name {
  display: none;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: var(--black-color);
  margin-right: 16px;

  @include tablet{
    display: block;
  }
}