.cabinet {
    padding-top: 24px;
    padding-bottom: 120px;

    @include tablet{
        padding-top: 32px;
        padding-bottom: 160px;
    }

    &__header {
        display: none;

        @include widescreen{
            display: block;
        }
    }

    &__wrapper {
        display: flex;
    }

    &__nav {
        min-width: 177px;
        margin-right: 123px;
        display: none;

        @include widescreen{
            display: block;
        }
    
    }

    &__content {
        width: 100%;
    }  

    &__title-mobile {
        font-weight: 700;
        font-size: 22px;
        line-height: 120%;
        color: var(--black-color);
        margin-bottom: 24px;

        @include tablet{
            font-size: 32px;
            margin-bottom: 32px;
        }

        @include widescreen{
            display: none;
        }
    }

    &__btn {
        display: block;
        text-align: left;
        font-weight: 400;
        font-size: 18px;
        line-height: 120%;
        color: #133D4D;
        &:not(:last-child) {
            margin-bottom: 20px;
        }

        &.active {
            font-weight: 700;
            color: var(--green-color);

        }
    }

    &__item {
        display: none;
        &.active {
            display: block;
        }
    }


   
}

.user {
   
    @include tablet{
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }

    &__forms {
        margin-bottom: 56px;

        @include tablet{
            min-width: 415px;
            margin-bottom: 0px;
        }

        @include widescreen{
            min-width: 460px;
        }
        
    }

    &__promo {
        padding: 26px 12px 40px 12px;
        background-color: #F6F8FC;
        border-radius: 8px;

        @include tablet{
            max-width: 260px;
            padding: 40px 16px;
        }

        @include widescreen{
            max-width: 280px;
        }

        &-top {
            margin-bottom: 50px;
        }

        &-points {
            display: block;
            font-weight: 400;
            font-size: 18px;
            line-height: 120%;
            color: var(--black-color);
            margin-bottom: 18px;

            span {
                font-weight: 700;
                font-size: 22px;
                padding: 0 8px;
            }
        }

        &-bottom {
            .user__subtitle {
                margin-bottom: 16px;
            }
        }

        &-text {
            font-weight: 400;
            font-size: 14px;
            line-height: 16px;
            color: var(--gray-color);
            margin-bottom: 14px;
        }
    }

    &__subtitle {
        margin-bottom: 24px;

        @include tablet{
            margin-bottom: 32px;
        }
    }

}

.cabinet-subtitle {
    font-weight: 700;
    font-size: 18px;
    line-height: 115%;
    color: var(--black-color);

    @include tablet{
        font-size: 22px;
        line-height: 120%;
    }
}

.history {
    margin-bottom: 64px;

    @include tablet{
        margin-bottom: 80px;
    }

    &__item {
      &:not(:last-child) {
        margin-bottom: 24px;
      }
    }

    &__wrap {
        background-color: var(--white-color);
        border: 1px solid var(--gray-semilight-color);
        border-radius: 8px;
        padding: 16px 8px;

      

        @include tablet{
            padding: 24px 20px;
        }

        &-top {
            display: flex;
            justify-content: space-between;
           
            @include tablet{
                margin-bottom: 22px;
            }

            &__right {
                display: flex;
            }
        }

        &-bottom {
        

            @include phone {
                display: flex;
                justify-content: space-between;
                align-items: flex-end;
            }

            @include tablet {
               align-items: center;
            }

            &__left {
                margin-bottom: 24px;

                @include phone{
                    margin-bottom: 0px;
                }
            }

            &__right {
                display: flex;
                flex-direction: column-reverse;
                align-items: flex-start;

                .btn--secondary {
                    margin-bottom: 8px;
                }

                .btn--underline {
                    max-width: 164px;
                }

                @include phone{
                    flex-direction: column;
                }

                @include phone{
                    .btn--secondary {
                        margin-bottom: 0px;
                    }
    
                    .btn--underline {
                        max-width: 164px;
                        margin-bottom: 10px;
                    }
                }

                @include tablet{
                    align-items: center;
                    flex-direction: row;

                    .btn--underline {
                        min-width: 164px;
                        margin-right: 16px;
                        margin-bottom: 0;
                    }
    
                    .btn--secondary {
                        max-width: 228px;
                        margin-bottom: 0;
                    }
                }

              
            }
        }
    }

    &__number {
        display: block;
        font-weight: 700;
        font-size: 16px;
        line-height: 24px;
        color: var(--black-color);
        margin-bottom: 8px;

        @include tablet{
            font-size: 18px;
            line-height: 115%;
            margin-bottom: 22px;
        }
      
    }

    &__list {
        li {
            font-weight: 400;
            color: var(--black-color);
            font-size: 14px;
            line-height: 20px;

            @include tablet{
                font-size: 16px;
                line-height: 24px;
            }
            &:not(:last-child) {
                margin-bottom: 3px;
            }

            .history__status {
                font-weight: 700;
                color: var(--green-color);
            }
        }
    }

    &__img {
        display: none;
        border: 1px solid #F5F5F5;
        border-radius: 4px;

        @include phone{
            display: block;
            width: 60px;
            height: 60px;
        }

        @include tablet{
            width: 100px;
            height: 100px;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }

        &:not(:first-child) {
            margin-left: 8px;
        }
    }

    &__sum {
        font-weight: 700;
        font-size: 14px;
        line-height: 20px;
        color: var(--black-color);
        
        @include tablet{
            font-size: 18px;
            line-height: 115%;
        }
    }

    &__table {
        display: none;
        padding: 20px 8px 18px 8px;
        background-color: var(--gray4-color);
        border-bottom-right-radius: 8px;
        border-bottom-left-radius: 8px;

        .btn--underline {
            max-width: 152px;
        }
        @include tablet{
            padding: 32px 20px;
        }

        &.active {
            display: block;
        }
        
        &-head {
            display: none;

            @include tablet{
                display: grid;
                grid-template-columns: repeat(12,1fr);
                padding-bottom: 15px;
            }

            span {
                display: block;
                grid-column: 2 span;
                font-weight: 700;
                font-size: 14px;
                line-height: 20px;
                color: var(--black-color);
                text-align: center;

                &:nth-child(1) {
                    grid-column: 2/4;
                }

                &:nth-child(2) {
                    grid-column: 4/7;
                    text-align: left;
                }

                &:nth-child(3) {
                    grid-column: 7/9;

                    @include widescreen{
                        grid-column: 8/9;
                    }
                }

                &:nth-child(4) {
                    grid-column: 10/10;
                }

                &:nth-child(5) {
                    grid-column: 11/13;
                }
            }
        }

        &-item {
            display: grid;
            grid-template-columns: 1fr 1fr;
            align-items: center;
            &:not(:last-child) {
                margin-bottom: 32px;
            }

            @include tablet{
                grid-template-columns: repeat(12,1fr);

                &:not(:last-child) {
                    margin-bottom: 12px;
                }
            }

            span {
              
               font-weight: 400;
                font-size: 14px;
                line-height: 20px;
                color: var(--black-color);

                @include tablet{
                    text-align: center;
                }

                &:nth-child(1) {
                    grid-column:2 span;
                    border: 1px solid #F5F5F5;
                    border-radius: 4px;

                    @include tablet{
                        grid-column: 1/1;
                    }

                    img {
                   
                        object-fit: contain;

                        @include tablet{
                            width: 100%;
                            height: 100%;
                        }
                    }
                }

                &:nth-child(2) {
                    grid-column: 2/4;
                    display: none;

                    @include tablet{
                        display: inline;
                    }
                }

                &:nth-child(3) {
                    grid-column: 1/2;
                    @include tablet{
                        grid-column: 4/7;
                        text-align: left;
                    }
                }

                &:nth-child(4) {
                    grid-column: 1/2;

                    @include tablet{
                        grid-column: 7/9;
                    }

                    @include widescreen{
                        grid-column: 8/9;
                    }
                }

                &:nth-child(5) {
                    grid-column: 1/2;

                    @include tablet{
                        grid-column: 10/11
                    }
                }

                &:nth-child(6) {
                    grid-column: 1/2;

                    @include tablet{
                        grid-column: 11/13;
                    }
                }
            }
        }

        &-mob {
            font-weight: 700 !important;

            @include tablet{
                display: none;
            }
        }
    }
}

.documents {
    &__select {
        max-width: 480px;
        margin-bottom: 32px;

        &-name {
            display: block;
            font-weight: 400;
            font-size: 14px;
            line-height: 120%;
            color: var(--black-color);
            margin-bottom: 4px;
        }

        .select {
            width: 100%;

            &__head {
                font-weight: 400;
                font-size: 16px;
                line-height: 115%;
                height: 34px;
                padding: 9px 0px 9px 8px;

                @include tablet{
                    height: 44px;
                }
               
            }

            &__text {
                font-size: 14px;
                line-height: 115%;

                @include tablet{
                    font-size: 16px;
                }
            }
        }
    }

    &__subtitle {
        display: block;
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        color: var( --gray-color);
        margin-bottom: 16px;
    }

    &__table {
        border: 1px solid var(--gray-light-color);
        border-radius: 8px;

        &-head {
            display: none;
            @include tablet{
                padding: 18px 20px;
                display: grid;
                grid-template-columns: repeat(12,1fr);
            }
            &:not(:last-child) {
                border-bottom: 1px solid var(--gray-light-color);
            }

            span {
                font-weight: 700;
                font-size: 14px;
                line-height: 20px;
                color: var(--black-color);

                grid-column: 3 span;

                &:nth-child(2) {
                    grid-column: 4/8;
                }

                &:nth-child(3) {
                    grid-column: 8/10;
                }

                &:nth-child(4) {
                    grid-column: 10/13;
                }
            }
        }

        &-item {
            padding: 24px 8px;
            display: grid;
            grid-template-columns: repeat(4,1fr);
            border-bottom: 1px solid var(--gray-light-color);
            align-items: center;

            @include tablet{
                grid-template-columns: repeat(12,1fr);
                padding: 15px 20px;
            }

            &__wrap {
              
                &:nth-child(1) {
                    grid-column: 4 span;
                    @include tablet{
                        grid-column: 1/4;
                    }
                }
               
                &:nth-child(2) {
                    grid-column: 4 span;
                    @include tablet{
                        grid-column: 4/8;
                    }
                }

                &:nth-child(3) {
                    grid-column: 4 span;
                    @include tablet{
                        grid-column: 8/10;
                    }
                }

                &:nth-child(4) {
                    grid-column: 4 span;
                    @include tablet{
                        grid-column: 10/13;
                    }
                }

                span {
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 20px;
                    color: var(--black-color);
                }
            }
        
        }

        &-bottom {
            display: grid;
            grid-template-columns: repeat(12,1fr);
            border-bottom: 1px solid var(--gray-light-color);
            padding: 15px 20px;
            align-items: center;
            span {
                font-weight: 400;
                font-size: 14px;
                line-height: 20px;
                color: var(--black-color);
                &:nth-child(1) {
                    grid-column: 1/4;
                }

                &:nth-child(2) {
                    grid-column: 10/13;
                }
            }
        }

        &-btn {
            color: var(--green-color);
            font-weight: 400;
            font-size: 14px;
            line-height: 16px;
            position: relative;
            margin-top: 16px;

            @include tablet{
                margin-top: 0px;
            }

            &::after {
                content: "";
                position: absolute;
                bottom: 0;
                left: 0;
                z-index: 1;
                width: 100%;
                height: 1px;
                background-color: var(--green-color);
            }
        }

        &-mob {
            font-weight: 700 !important;
            
            @include tablet{
                display: none;
            }
        }
    }
}

.wait {        
    color: var(--gray-color) !important;
}


.documents__upload {
	position: relative;
	overflow: hidden;
    max-width: 87px;
    margin-top: 16px;

    @include tablet{
        margin-top: 0;
    }

    &-text {
        color: #10DBB6 !important;
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;
    }


    &::after {
        content: "";
        position: absolute;
        bottom: 2px;
        left: 0;
        z-index: 1;
        width: 100%;
        height: 1px;
        background-color: var(--green-color);
    }

    &-input {
        position: absolute;
        top: 0;
        right: 0;
        margin: 0;
        padding: 0;
        font-size: 20px;
        cursor: pointer;
        opacity: 0;
        filter: alpha(opacity=0);
    }

}

#fileuploadurl{
	display: inline-block;
	border:none;
	vertical-align: middle;
	background: none;
	box-shadow: none;
	font-size: 11px;
	padding-left: 0;
  width:220px;
	padding-right: 0;
}