.delivery {
    padding-top: 24px;
    padding-bottom: 85px;
    @include tablet {
        padding-top: 0;
        padding-bottom: 50px;
    }

    &__header {
        margin-bottom: 24px;

        @include tablet {
            margin-bottom: 32px;
        }

        @include widescreen {
            margin-bottom: 40px;
        }
    }

    &__wrapper {
        display: flex;
        position: relative;
    }

    &__main {
        @include widescreen {
            padding-left:85px;
        }
    }

    &__nav {
        display: none;
        min-width: 219px;

        @include widescreen {
            display: block;
        }
    }
    &-nav {
        &__item {
            &:not(:last-child) {
                margin-bottom: 16px;
            }

            &-link {
                font-weight: 700;
                font-size: 18px;
                line-height: 115%;
                color: var(--gray-color);

                &.active {
                    color: var(--green-color);
                }
            }
        }
    }

    &__info {
        margin-bottom: 80px;
        &-text {
            line-height: 125%;

            &:first-child {
                max-width: 581px;
                margin-bottom: 24px;

                @include tablet {
                    margin-bottom: 32px;
                }
            }

            &:last-child {
                margin-bottom: 0;
            }

            span {
                &:first-child {
                    font-weight: 700;
                }

                &:last-child {
                    display: flex;
                    align-items: center;
                   
                    .btn--underline {
                        max-width: 70px;
                        margin-left: 5px;
                    }
                }
            }
        }
    }

    &__item {
        &:not(:last-child) {
            margin-bottom: 64px;
        }
    }

    &__title {
        font-weight: 700;
        font-size: 22px;
        line-height: 120%;
        color: var(--black-color);
        margin-bottom: 12px;

        @include tablet {
            font-size: 32px;
            margin-bottom: 16px;
        }
    }

    &__subtitle {
        font-weight: 700;
        font-size: 18px;
        line-height: 120%;
        color: var(--green-active-color);
        margin-bottom: 12px;
        
        @include tablet {
            font-size: 22px;
            margin-bottom: 16px;
        }
    }

    &__list {
        margin-bottom: 40px;

        @include tablet {
            margin-bottom: 64px;
        }
        &-item {
            &:not(:last-child) {
                margin-bottom: 9px;
            }
            font-weight: 400;
            font-size: 14px;
            line-height: 125%;
            color: var(--black-color);
            position: relative;
            padding-left: 17px;
            @include tablet {
                font-size: 16px;
            }

            &::before {
                content: "";
                position: absolute;
                top: 4px;
                left: 0;
                z-index: 1;
                width: 8px;
                height: 8px;
                border-radius: 100%;
                background-color: var(--green-color);
                margin-right: 12px;
            }

        }
    }

    &__important {
        background-color: #EEFCFA;
        border-radius: 15px;
        padding: 12px 8px;

        @include tablet {
            padding: 20px 20px;
        }

        span {
            display: block;
            font-weight: 700;
            font-size: 16px;
            line-height: 115%;
            color: var(--black-color);
            margin-bottom: 8px;

            @include widescreen {
                font-size: 18px;
            }
        }

        &-text {
            &:last-child {
               margin-bottom: 0;
            }
        }
    }

    &__price {
        max-width: 565px;
        margin-bottom: 32px;
        li {
            padding: 0 0px 6px 0px;
          
            border-bottom: 1px solid var(--gray-light-color);
            margin-bottom: 4px;

            @include tablet {
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 0 8px 14px 8px;
            }
            
            span {
                display: block;
                font-weight: 400;
                font-size: 14px;
                line-height: 115%;
                color: var(--black-color);

                @include tablet {
                    font-size: 16px;
                }

                &:first-child {
                    margin-bottom: 4px;

                    @include tablet {
                        margin-bottom: 0px;
                    }
                }

                &:last-child {
                    font-weight: 700;
                }
            }
        }
    }

    &-text {
        font-weight: 400;
        font-size: 16px;
        line-height: 20px;
        color: var(--black-color);
        margin-bottom: 20px;
        
        @include tablet {
            font-size: 16px;
            margin-bottom: 24px;
        }
    }

    &__wrap {
        margin-bottom: 80px;

        @include tablet {
            margin-bottom: 120px;
        }
    }

    &__question {
     
    }

}

.nav-top {
    position: fixed;
    top: 83px;
    left: calc((100% - var(--content-width)) / 2);
    width: 219px;
}

.nav-bottom {
    position: absolute;
    top: auto;
    bottom: 00px;
    left: calc((100% - var(--content-width))/ 2);
}