.footer {
    padding-top: 32px;
    padding-bottom: 32px;
    background-color: var(--gray4-color);

    @include tablet{
        padding-top: 38px;
        padding-bottom: 31px;
    }

    @include desktop{
        padding-bottom: 48px;
    }

    &__phone {
        margin-bottom: 24px;

        @include tablet{
            margin-bottom: 36px;
        }

        a {
            font-weight: 700;
            font-size: 18px;
            line-height: 27px;
            color: var(--black-color);
        }

        span {
            display: block;
            font-weight: 400;
            font-size: 12px;
            line-height: 18px;
            color: var(--gray-color);
        }
    }

    &__bottom {
        display: grid;
        grid-template-columns: repeat(6,1fr);

        @include tablet{
            grid-template-columns: repeat(12,1fr);
        }

    }

    &__item {
        &:nth-child(1) {
            grid-column: 4 span;

            @include tablet{
                display: block;
            }

            @include desktop{
                grid-column: 5 span;
            }
        }

        &:nth-child(2) {
            grid-column: 2 span;
            display: none;

            @include tablet{
               display: block;
            }
        }

        &:nth-child(3) {
            display: none;

            @include tablet{
               display: block;
               grid-column: 3 span;
            }

            @include desktop{
                grid-column: 2 span;

            }
        }

        &:nth-child(4) {
            grid-column: 6 span;
            display: flex;
            

            @include tablet{
                grid-column: 3 span;
                justify-content: flex-end;
            }
        }
    }

    .footer__address {
        font-weight: 400;
        font-style: normal;
        font-size: 12px;
        line-height: 18px;
        color: var(--black-color);
        margin-bottom: 10px;

        span {
            display: block;
            
            &:not(:last-child){
                margin-bottom: 4px;
            }
        }

        a {
            font-weight: 400;
            font-style: normal;
            font-size: 12px;
            line-height: 18px;
            color: var(--black-color);
        }
    }

    &__nav {

        &-item {
            &:not(:last-child) {
                margin-bottom: 4px;
            }
            a {
                font-weight: 400;
                font-size: 12px;
                line-height: 18px;
                color: var(--black-color);
            }
        }

        &-mobile {
            margin-bottom: 24px;

            @include tablet{
              display: none;
            }
        }
    }

    &__payment {
       
        @include tablet{
         margin-bottom: 22px;
        }
        span {
            display: block;
            font-weight: 400;
            font-size: 12px;
            line-height: 18px;
            color: var(--gray-color);
            margin-bottom: 5px;
        }
    }

    &__copyright {
    
        span {
            display: block;
            font-weight: 400;
            font-size: 12px;
            line-height: 18px;
            color: var(--gray-color);
            

            &:not(:last-child) {
                margin-bottom: 4px;
            }
        }

        a {
            font-weight: 400;
            font-size: 12px;
            line-height: 18px;
            color: var(--gray-color);
            // margin-bottom: 4px;
        }
    }

    &__wrap {
        display: flex;
        justify-content: space-between;
        width: 100%;

        @include phone{
            max-width: 360px;
        }

        @include tablet{
           display: block;
           width: auto;
        }
    }
}

.socials {
    display: flex;
    margin-bottom: 24px;

    @include tablet{
        margin-bottom: 0px;
    }

    &__item {
        &:not(:last-child) {
            margin-right: 12px;
        }
    }
}

.payment-list {
    display: flex;
    justify-content: space-between;
}