.order-steptwo {
    margin-bottom: 80px;
    &__title {
        font-weight: 700;
        font-size: 26px;
        line-height: 120%;
        color: var(--black-color);
        margin-bottom: 16px;
    }

    &__wrapper {
        border: 1px solid var(--gray-light-color);
        border-radius: 8px;
        margin-bottom: 24px;
    }
    

    &__article {
        grid-column: 6/8;
        text-align: center;
        display: none;
        span {
            font-weight: 700;
            font-size: 16px;
            line-height: 100%;
            color: #133D4D;
        }

        @include widescreen {
           display: block;
        }
    }

    &__img {

        @include tablet {
            grid-column: 2 span;
        }
    }

    &__info {
        grid-column: 4 span;

        @include tablet {
            grid-column: 3 span;
        }
    }

    &__name {
        @include tablet {
            margin-bottom: 0;
        }
    }

    &__price {
        grid-column: 4 span;
        display: block;
        margin-bottom: 8px;

        @include tablet {
            grid-column: 7/9;
            margin-bottom: 0px;
        }

        @include widescreen {
            grid-column: 8/10;
        }
    }

    &__sum {
        display: block;
        grid-column: 4 span;

        @include tablet {
            grid-column: 11/13;
        }
    }

    &__amount {
        display: block;
        grid-column: 4 span;
       

        @include tablet {
            grid-column: 9/11;
        }

        @include widescreen {
            grid-column: 10/11;
        }
    }

    .btn--underline {
        max-width: 150px;
    }
}

.order {

    &__data {

        &-title {
            font-weight: 700;
            font-size: 16px;
            line-height: 100%;
            color: var(--black-color);
            margin-bottom: 20px;

            @include tablet {
                font-size: 22px;
                margin-bottom: 31px;
            }
            
        }

        &-finnaly__sum {
            font-weight: 700;
            font-size: 18px;
            line-height: 100%;
            color: var(--black-color);
            margin-bottom: 20px;

            @include tablet {
                font-size: 22px;
                margin-bottom: 31px;
            }
        }

        &-buttons {
            display: flex;
            flex-direction: column;
            

            @include tablet {
                flex-direction: row;
                align-items: center;
            }

            .btn--secondary {
                max-width: 354px;
                margin-bottom: 12px;

                @include tablet {
                    margin-bottom: 0px;
                }
            }

            .btn--underline {
                max-width: 127px;
                height: 24px;
              
                @include tablet {
                    margin-left: 32px;
                }
            }
        }
    }

    &__table {
        margin-bottom: 24px;
    }

    &__head {
        &-item {
            &:nth-child(1) {
                grid-column: 6/8;

                @include tablet {
                    grid-column: 3/4;
                }
                
                @include widescreen {
                    grid-column: 3/3;
                }
            
            }

            &:nth-child(2) {
                grid-column: 8/10;
                display: none;
                @include widescreen {
                    grid-column: 6/8;
                    display: block;
                }
            }

            &:nth-child(3) {
                grid-column: 8/10;
                display: block;
    
                @include tablet {
                    grid-column: 7/9;
                }

                @include widescreen {
                    grid-column: 8/10;
                }
            
            }

            &:nth-child(4) {
                grid-column: 10/11;
                @include tablet {
                    grid-column: 9/11;
                }
               
                @include widescreen {
                    grid-column: 10/11;
                }
            }
    
    
            &:nth-child(5) {
                grid-column: 11/13;
            }
        }
    }

  
    &__article {
        grid-column: 6/8;
        text-align: center;
        display: none;
        span {
            font-weight: 700;
            font-size: 16px;
            line-height: 100%;
            color: #133D4D;
        }

        @include widescreen {
           display: block;
           margin-bottom: 0;
        }
    }

    &__img {

        @include tablet {
            grid-column: 2 span;
        }
    }

    &__info {
        grid-column: 4 span;

        @include tablet {
            grid-column: 3 span;
        }
    }

    &__name {
        @include tablet {
            margin-bottom: 0;
        }
    }

    &__price {
        grid-column: 4 span;
        display: block;
        margin-bottom: 8px;

        @include tablet {
            grid-column: 7/9;
            margin-bottom: 0px;
        }

        @include widescreen {
            grid-column: 8/10;
        }
    }

    &__sum {
        display: block;
        grid-column: 4 span;

        @include tablet {
            grid-column: 11/13;
        }
    }

    &__amount {
        display: block;
        grid-column: 4 span;
       

        @include tablet {
            grid-column: 9/11;
        }

        @include widescreen {
            grid-column: 10/11;
        }
    }

    .btn--underline {
        max-width: 150px;
    }

}

.data-list {
    margin-bottom: 22px;
    @include tablet {
        margin-bottom: 31px;
    }
    &__item {
        display: flex;
        &:not(:last-child) {
            margin-bottom: 10px;

            @include phone {
                margin-bottom: 8px;
            }

            @include tablet {
                margin-bottom: 12px;
            }
        }
        span {
            display: block;
            font-weight: 400;
            font-size: 14px;
            line-height: 100%;
            color: var(--black-color);

            @include tablet {
                font-size: 18px;
            }

            &:first-child {
                flex-shrink: 0;
            }
        }
    }
}