/* stylelint-disable */

@mixin custom-checkbox {
  position: relative;
  display: flex;
  align-items: center;

  &__field {
    appearance: none;
    position: absolute;
  }

  &__text {
    font-weight: 400;
    font-size: 12px;
    line-height: 100%;
    color: var(--gray-color);
    display: block;

    a {
      color: inherit;
      font-size: inherit;
      line-height: inherit;
      font-weight: inherit;
      position: relative;

      &::after {
        content: "";
        position: absolute;
        bottom: 2px;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: var(--gray-color);
      }
    }
  }

  &__content {
    padding-left: 25px;
    cursor: pointer;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      border: 1px solid var(--gray-color);
      width: 16px;
      height: 16px;
      box-sizing: border-box;
      border-radius: 2px;
      border: 1px solid #96ACB3;
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 16px;
      height: 16px;
      box-sizing: border-box;
      background-image: url("../img/cheked.svg");
      background-position: center;
      background-size: 10px 10px;
      background-repeat: no-repeat;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
      border-radius: 2px;
    }
  }
}

.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
  background-color: var(--green-color);
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  // outline: 2px solid #f00;
  // outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}
