  justify-content: center;
  width: 24px;
  height: 24px;
  border-radius: 100%;
  background: transparent;
}

.tooltip__txt {
  position: absolute;
  left: -7px;
  top: calc(50% + 14px);
  z-index: 3;
  padding: 8px 14px;
  background: #FFFFFF;
  border: 1px solid #D9E4E7;
  border-radius: 4px;
  width: max-content;
  max-width: 211px;
  font-weight: 400;
  font-size: 12px;
  line-height: 110%;
  color: #133D4D;
  background: #fff;
  opacity: 0;
  visibility: hidden;
  transform: translateY(10px);
  text-transform: none;
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

.tooltip__btn:hover + .tooltip__txt {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.tooltip__btn:focus + .tooltip__txt {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

