.select__item.active {
  color: var(--green-color);
}
.select__item:hover {
  background-color: var(--gray4-color);
}
.select__img {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 10px;
}
.select__img img {
  height: 100%;
  object-fit: cover;
}

.select-wrapper {
  display: flex;
  align-items: center;
}

.select-name {
  display: none;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: var(--black-color);
  margin-right: 16px;
}
@media (min-width: 768px) {
  .select-name {
    display: block;
  }
}