.contacts {
    padding-top: 24px;
    padding-bottom: 80px;

    @include tablet {
        padding-top: 0;
        padding-bottom: 120px;
    }

    &--gray {
        background-color: var(--gray4-color);
        padding: 40px 0;

        @include tablet{
            padding: 64px 0;
        }

        @include widescreen{
            padding: 76px 0;
        }

       .contacts__title {
        margin-bottom: 16px;
       }
    }

    &--requisites {
        padding-top: 80px;
        padding-bottom: 45px;

        @include tablet {
            padding-top: 120px;
            padding-bottom: 50px;
        }
        .contacts__container {

            @include widescreen {
                display: flex;
                justify-content: space-between;
            }
            
        }
    }

    &__title {
        font-weight: 700;
        font-size: 22px;
        line-height: 120%;
        color: var(--black-color);
        margin-bottom: 24px;

        @include tablet {
            font-size: 32px;
            margin-bottom: 32px;
        }
    }

    &__contact {
        margin-bottom: 80px;

        @include tablet {
            margin-bottom: 120px;
        }
    }

    &__item {

        @include phone {
            display: flex;
        }
        &:not(:last-child) {
            margin-bottom: 16px;
        }

        &-name {
            display: inline-block;
            font-weight: 400;
            font-size: 14px;
            line-height: 115%;
            color: var(--black-color);
            min-width: 171px;

            @include phone {
                font-size: 16px;
            }
        }

        &-contact {
            display: block;
            font-weight: 700;
            font-size: 14px;
            line-height: 18px;
            color: var(--black-color);

            @include phone {
                font-size: 16px;
                display: inline;
            }
        }
    }

    &__getthere {
     
        .section-title {
            margin-bottom: 24px;
        }

        &-wrap {
            display: flex;
            flex-direction: column-reverse;
            @include widescreen {
                flex-direction: row;
                align-items: flex-start;
                justify-content: space-between;
            }
        }
    }

    &__map {
        margin-bottom: 40px;
        @include widescreen {
            min-width: 544px;
            margin-bottom: 0px;
        }
    }

    &__about {
      
        @include widescreen{
            display: flex;
            justify-content: space-between;
        }

        &-item {
            
            &:not(:last-child) {
                margin-bottom: 40px;
            }

            @include tablet{
                &:not(:last-child) {
                    margin-bottom: 48px;
                }
            }
                @include widescreen{
                    &:not(:last-child) {
                        margin-bottom: 0px;
                    }
                    max-width: 501px;
                }
            p {
                font-weight: 400;
                font-size: 14px;
                line-height: 24px;
                color: var(--black-color);

                @include tablet{
                    font-size: 16px;
                }

                &:not(:last-child) {
                    margin-bottom: 22px;
                }

                span {
                    font-weight: 700;
                }

                a {
                    font-weight: 700;
                    font-size: 16px;
                    line-height: 24px;
                    color: var(--black-color);
                }
            }
        }

        &-wrap {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
        }
    }
}

.requisites {

    &__title {
        font-weight: 700;
        font-size: 22px;
        line-height: 120%;
        color: var(--black-color);
        margin-bottom: 24px;

        @include tablet{
            font-size: 32px;
        }
    
    }

    &-list {
        margin-bottom: 80px;

        @include widescreen{
            margin-bottom: 0;
        }
    
        &__item {
            @include phone {
                display: flex;
            }
            &:not(:last-child) {
                margin-bottom: 16px;
            }
        }
    
        &__name {
            display: inline-block;
            font-weight: 400;
            font-size: 14px;
            line-height: 115%;
            color: var(--black-color);
            min-width: 169px;
    
            @include phone {
                font-size: 16px;
            }
        }
    
        &__info {
            display: block;
            font-weight: 700;
            font-size: 14px;
            line-height: 115%;
            color: var(--black-color);
    
            @include phone {
                font-size: 16px;
                display: inline;
            }
        }
    }
}

.questions {
    max-width: 487px;
    &__title {
        font-weight: 700;
        font-size: 22px;
        line-height: 120%;
        color: var(--black-color);
        margin-bottom: 12px;

        @include tablet{
            font-size: 32px;
            margin-bottom: 8px;
        }
    
    }

    &__subtitle {
        display: block;
        font-weight: 400;
        font-size: 14px;
        line-height: 115%;
        color: var(--black-color);
        margin-bottom: 24px;

        @include tablet{
            font-size: 16px;
            margin-bottom: 32px;
        }
    }
}

.contacts-map {
    height:167px;
    border-radius: 8px;
    overflow: hidden;
 
    @include phone{
        height: 260px;
    }

    @include tablet {
        height: 426px;
    }

    @include widescreen {
        height: 426px;
    }
}


.accordion-wrap {
    
    @include widescreen {
        max-width: 486px;
    }
}
  
.accordion {
    &:nth-of-type(3) {
        border-bottom: 1px solid #D9E4E7;

        .accordion__descr {
            padding-bottom: 24px;
        }
    }
  
    &__title {
        position: relative;
        cursor: pointer;
        padding: 13.5px 0 19.5px 0;
        width: 100%;
        text-align: left;
        border: none;
        outline: none;
        border-top: 1px solid #D9E4E7;
        font-weight: 700;
        font-size: 18px;
        line-height: 120%;
        color: var(--black-color);

        @include tablet {
            font-size: 22px;
            padding: 27px 0 21px 0;
        }
  
      &--more {
        &:after {
            content: "";
            position: absolute;
            z-index: 1;
            top: 50%;
            right: 1.2rem;
            width: 20px;
            height: 10px;
            transform: translateY(-50%);
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
            transition: all 0.3s ease;
            background-image: url("../img/arrow-down.svg");
        }
  
        &.active {
          &:after {
            transform: translateY(-50%) rotate(180deg);
          }
        }
      }
      &.active {
        
        & + .accordion__content {
          max-height: 1000px;
        }
      }
    }
    &__content {
        max-height: 0;
        overflow: hidden;
        transition: all 0.3s;
        background-color: white;
        width: calc(100% - 4px);
        margin: 0 auto;
    }
  
    &__descr {
        font-weight: 400;
        font-size: 14px;
        line-height: 24px;
        color: var(--black-color);
        padding-bottom: 12px;

        @include tablet {
            font-size: 16px;
            padding-bottom: 24px;
        }

    }
  
}
  