.product {
  padding-top: 24px;

  @include widescreen {
    padding-top: 0;
  }

    &__wrapper {
        margin-bottom: 48px;

        @include tablet {
          margin-bottom: 120px;
        }
        @include widescreen {
          display: flex;
          margin-bottom: 160px;
        }
    }

    &__left {
      margin-bottom: 40px;

      @include widescreen {
        width: 50%;
        margin-bottom: 0px;
      }
    }

    &__swiper {
      height: 223px;
      margin-bottom: 16px;
      border: 1px solid #F5F5F5;
      border-radius: 8px;

      @include phone {
        height: 347px;
      }

      @include widescreen {
        height: 471px;
        margin-bottom: 22px;
      }

      @include widescreen {
        height: 436px;
        margin-bottom: 26px;
      }

      &.minus {

        .shield-minus {
          top: 8px;
          left: 8px;
          display: inline-flex;
          z-index: 10;
        }
      }

      .swiper-button {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        z-index: 10;
        background-color: transparent;

        &.product__next {
          right: 0;
          background-image: url("../img/arrow-black.svg");
          background-repeat: no-repeat;
          background-size: 14px 14px;
          background-position: center center;
         
        }

        &.product__prev {
          left: 0;
          background-image: url("../img/arrow-black-left.svg");
          background-repeat: no-repeat;
          background-size: 12px 12px;
          background-position: center center;
          
        }
      }

      .swiper-slide img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: contain;
      }

      &-nav {
        height: 72px;

        @include phone {
          height: 83px;
        }

        @include tablet {
          height: 102px;
        }
          .swiper-slide img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
          }

          .swiper-slide-thumb-active {
            border: 1px solid #10DBB6;
            border-radius: 4px;
          }
        
      }
    }

    &__right {

      @include widescreen {
        width: 50%;
        margin-left: 40px;
      }

    }

    &__article {
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        color: var(--gray-color);
        margin-bottom: 9px;
    }

    &__stocks {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 8px;

        @include widescreen {
          margin-bottom: 18px;
        }
        .shield {
          margin-bottom: 10px;

          @include widescreen {
            margin-bottom: 0px;
          }
        }
        &.points-10 {
            .shield-points-10 {
              display: flex;
            }
          }
      
          &.points-30 {
            .shield-points-30 {
              display: flex;
            }
          }
      
          &.points-70 {
            .shield-points-70 {
              display: flex;
            }
          }
      
          &.new {
            .shield-new {
              display: flex;
            }
          }
      
          &.sale {
            .shield-sale {
              display: flex;
            }
          }
      
          &.points {
            .shield-points {
              display: flex;
            }
          }
      
          &.not {
            .shield-notavalible {
              display: flex;
            }
          }
    }

    &__title {
        font-weight: 700;
        font-size: 18px;
        line-height: 120%;
        color: var(--black-color);
        margin-bottom: 24px;

        @include tablet {
          font-size: 22px;
          margin-bottom: 26px;
        }
    }

    &__price {
        margin-bottom: 16px;

        @include phone {
          margin-bottom: 10px;
        }

        @include tablet {
          margin-bottom: 17px;
        }

        &-current {
            font-weight: 700;
            font-size: 26px;
            line-height: 120%;
            color: var(--black-color);

            @include phone {
              font-size: 32px;
            }

            @include tablet {
              font-size: 40px;
            }
        }

        &-old {
            font-weight: 400;
            font-size: 18px;
            line-height: 120%;
            text-decoration-line: line-through;
            color: var(--gray-color);
            margin-left: 16px;

            @include phone {
              font-size: 18px;
              margin-left: 32px;
            }

            @include tablet {
              font-size: 22px;
            }
        }
    }

    &__buttons {
        display: flex;
        align-items: center;
        margin-bottom: 18px;

        .btn--primary {
            // max-width: 270px;
           
            width: 168px;
            padding: 10px 12px;

            @include phone {
              width: 270px;
              padding: 8px 40px;
            }

            span {
                flex-shrink: 1;
                padding-right: 12px;
                display: none;

                @include phone {
                  display: block;
                }
            }
        }

        .counter-wrapper {
          margin-left: 5px;
          border: 1px solid #D9E4E7;
          border-radius: 4px;

          
          @include phone {
            margin-left: 20px;
          }
        }
    }

    &__links {
      display: flex;
      align-items: center;
      padding-bottom: 16px;
      border-bottom: 1px solid #F5F5F5;

      @include tablet{
        padding-bottom: 28px;
      }
    }


    &__link {
      display: flex;
      align-items: center;
      &:last-child {
        margin-left: 50px;
      }

      @include phone{
        &:last-child {
          margin-left: 60px;
        }
      }

        &-text {
          font-weight: 400;
          font-size: 12px;
          line-height: 100%;
          color: var(--gray-semi-color);
          margin-left: 11px;

          @include phone {
            font-size: 14px;
          }
        }

    }

    &__generator {
      display: flex;
      padding-top: 16px;
      align-items: center;
      justify-content: space-between;
      max-width: 499px;

      @include phone{
        padding-top: 0px;
      }

      
      @include tablet{
        padding-top: 10px;
      }
  

        &-text {
          font-weight: 400;
          font-size: 14px;
          line-height: 20px;
          color: var(--black-color);
        }

        &-link {
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            color: var(--black-color);
            position: relative;

            &::after {
                content: "";
                position: absolute;
                bottom: 3px;
                left: 0;
                z-index: 1;
                width: 100%;
                height: 1px;
                background-color: var(--black-color);
            }
        }

        &-img {
          display: none;
          max-height: 60px;

          @include phone {
            display: block;
          }
        }
    }

    &__descr {
      display: none;

      @include widescreen {
        display: block;

        .btn--underline {
          max-width: 128px;
          color: var(--green-color);
        }

        .btn--underline::before {
          background-color: var(--green-color);
        }
       
        &-name {
            font-weight: 700;
            font-size: 16px;
            line-height: 24px;
            color: var(--black-color);
        }

        &-text {
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            color: #133D4D;
        }
      }
    }

    &__tabs {
      position: relative;
      margin-bottom: 160px;

      &::before {
        content: "";
        position: absolute;
        top: 33px;
        left: 0;
        z-index: 1;
        width: 100%;
        height: 1px;
        background-color: var(--gray-semilight-color);
        display: none;
        @include tablet {
          display: block;
        }
      }

      &-text {
        font-weight: 400;
        font-size: 14px;
        line-height: 24px;
        color: var(--black-color);
        max-width: 860px;

        @include tablet {
          font-size: 16px;
        }

        &:first-child {
          margin-bottom: 40px;
        }
      }
    }
   
}

.set-list {
  max-width: 670px;
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;

    &:not(:last-child) {
      margin-bottom: 29px;
    }

    &::after {
      content: "";
      position: absolute;
      bottom: -8px;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 1.4px;
      background-color: var(--gray-light-color);
    }
  }

  &__name {
    display: block;
    max-width: 210px;
    font-weight: 400;
    font-size: 14px;
    line-height: 120%;
    color: var(--black-color);

    
    @include phone {
      max-width: 400px;
    }

    @include tablet {
      max-width: 500px;
      font-size: 16px;
    }

  }

  &__amount {
    display: block;
    font-weight: 700;
    font-size: 14px;
    line-height: 120%;
    color: var(--black-color);
    text-align: right;
    max-width: 120px;

    @include tablet {
      font-size: 16px;
    }
  }
}

.download-list {
  max-width: 464px;
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 0;
    position: relative;

    @include tablet {
      padding-left: 23px;
    }

    &:not(:last-child) {
      margin-bottom: 26px;
    }

    &::after {
      content: "";
      position: absolute;
      bottom: -8px;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 1.4px;
      background-color: var(--gray-light-color);
    }

    &::before {
      content: "";
      position: absolute;
      top: 1px;
      left: 0;
      z-index: 1;
      background-image: url("../img/document.svg");
      background-repeat: no-repeat;
      background-size: cover;
      width: 14px;
      height: 18px;
      display: none;

      @include phone {
        display: block;
      }
    }
  }

  &__name {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: var(--black-color);
   
    @include phone {
      padding-left: 20px;
    }
    @include tablet {
      font-size: 16px;
      padding-left: 0px;
    }
  }

  &__link {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: var( --gray-semi-color);

    @include tablet {
      font-size: 16px;
    }
  }
}


.tabs-item {
  display: none;
  &.active {
    display: block;
  }
}


.swiper.product__swiper-nav {
  overflow: visible;
}


.related {
  margin-bottom: 120px;
  &__swiper {
    @include tablet {
      min-height: 466px;
    }
  }
}


.tabs-tab-list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.tabs-tab-list li {
  &:not(:first-child) {
    margin-left: 80px;
  }
}

.tabs-trigger {
  border-bottom: none;
  color: #96ACB3;
  display: none;
  font-weight: normal;
  margin: 0;
  padding: 0;
  text-decoration: none;
  font-weight: 700;
  font-size: 18px;
  line-height: 115%;
  position: relative;

  &.is-selected {
    &::before {
      content: "";
      position: absolute;
      bottom: -14px;
      left: 0;
      z-index: 1;
      width: 100%;
      height: 2px;
      background-color: var(--green-color);
    }
  }
}

.tabs-trigger.is-selected, .tabs-trigger.is-selected:hover, .tabs-trigger.is-selected:focus {
  color: var(--green-color)
}

.tabs-panel {
  display: block;
  margin: 0;
  padding: 0;

  &.is-open {
    .accordion-trigger {
      border-bottom: 1px solid #D9E4E7;
      color: var(--green-color);
    }

    .accordion-icon {
      transform: rotate(270deg);
      fill: var(--green-color);
    }
  }
}

.tabs-panel:first-of-type {
  border-top: 1px solid #D9E4E7;
  @include tablet {
    border-top: none;
  }
}

.tabs-panel:last-of-type {
  border-bottom: 1px solid #D9E4E7;
  @include tablet {
    border-bottom: none;
  }
}

.tabs-panel:not(:first-of-type) {
  border-top: 1px solid #D9E4E7;
  @include tablet {
    border-top: none;
  }
}


.tabs-panel .content {
  padding-top: 24px;
  padding-bottom: 40px;
  @include tablet {
    padding-top: 55px;
  }

}

.tabs-panel.is-hidden .content {
  display: none;
}

.tabs-panel:active, .tabs-panel:focus {
  outline: none;
}

.is-initialized.tabs-allowed .tabs-panel {
  display: inherit;
}

.accordion-trigger {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  padding: 10px 0;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  color: #96ACB3;
}

.accordion-trigger-icon {
  pointer-events: none;
}

@media (min-width: 767px) {
  .tabs-allowed .accordion-trigger {
    display: none;
  }
  .tabs-allowed .tabs-trigger {
    display: block;
  }
  .tabs-allowed .tabs-panel {
    display: none;
  }
  .tabs-allowed .tabs-panel.is-hidden {
    display: none;
  }
  .tabs-allowed .tabs-panel {
    //  border-top: 2px solid #eee;
  }
  .tabs-allowed .tabs-tab-list {
    margin-bottom: -2px;
  }
}

[aria-expanded="true"] .vert {
  display: none;
}

.accordion-icon {
  width: 15px;
  height: 15px;
  fill: #96ACB3;
  transform: rotate(90deg);
  margin-right: 10px;
  transition: all .3s ease;
  pointer-events: none;
  @include tablet {
   display: none;
  }
}