.login {
    padding-top: 24px;
    padding-bottom: 120px;

    @include tablet {
        padding-top: 32px;
        padding-bottom: 160px;
    }

    .section-title {
        margin-bottom: 24px;

        @include tablet {
            margin-bottom: 32px;
             text-align: center;
        }
    }
}

.wrapper {
    max-width: 360px;
    margin: 0 auto;

    &__btn {
        max-width: 151px;
        margin-right:auto;
        @include tablet {
            margin: 0 auto;
        }

        .btn--underline {
            &:first-child {
                margin-bottom: 4px;
            }
        }

        &--registration {
          max-width: 174px;
        }
    }
    
    &__subtitle {
        font-weight: 400;
        font-size: 16px;
        line-height: 125%;
        text-align: center;
        color: var(--black-color);
    }
}



.registration {
    padding-top: 24px;
    padding-bottom: 120px;

    @include tablet {
        padding-top: 32px;
        padding-bottom: 160px;
    }

    .section-title {
        margin-bottom: 24px;

        @include tablet {
            margin-bottom: 32px;
             text-align: center;
        }
    }

}

.restorepass {
    padding-top: 24px;
    padding-bottom: 120px;

    @include tablet {
        padding-top: 32px;
        padding-bottom: 160px;
        text-align: center;
    }

    .section-title {
        margin-bottom: 24px;

        @include tablet {
            margin-bottom: 32px;
             text-align: center;
        }
    }

    .btn--underline {
        max-width: 174px;
    }

}

.restorepass-success {
    padding-top: 24px;
    padding-bottom: 244px;

    @include tablet {
        padding-top: 176px;
        padding-bottom: 176px;
        text-align: center;
    }

    .section-title {
        margin-bottom: 21px;
    }
}