.offers {
    padding-top: 35px;
    padding-bottom: 40px;

    @include widescreen{
        padding-top: 112px;
        padding-bottom: 70px;
    }

    &__container {
        background-color: #D1F3F1;
        min-height: 200px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 4px;
        margin-bottom: 40px;
        position: relative;
        overflow: hidden;

        @include tablet{
            justify-content: flex-end;
            margin-bottom: 80px;
            min-height: 242px;
            border-radius: 12px;
        }

        @include widescreen{
            margin-bottom: 112px;
        }

        &::after {
            content: "";
            position: absolute;
            left: -5px;
            bottom: 0px;
            z-index: 1;
            width: 175px;
            height: 42px;
            background-image: url("../img/cloud.svg");
            background-repeat: no-repeat;
            background-size: cover;

            @include tablet{
                left: -84px;
                bottom: -1px;
                width: 231px;
                height: 82px;
            }
        }

        &::before {
            content: "";
            position: absolute;
            top: 20px;
            left: 40%;
            z-index: 1;
            width: 109px;
            height: 63px;
            background-image: url("../img/cloud-sm.svg");
            background-repeat: no-repeat;
            display: none;

            
            @include widescreen{
              display: block;
            }

        }
    }

    &__wrap {
     
        padding: 10px 15px 30px 15px;
        width: 100%;

        @include tablet{
            max-width: 405px;
        }
        
        @include desktop{
            padding-right: 100px; 
            max-width: 520px;
        }
    }

    &__title {
        font-weight: 700;
        font-size: 22px;
        line-height: 120%;
        color: var(--black-color);
        max-width: 282px;
        margin-bottom: 13px;

        @include tablet{
            font-size: 32px;
            margin-bottom: 27px;
            max-width: 401px;
        }
    }
    
    &__form {
        position: relative;
        .form__input {
            width: 100%;
            // max-width: 337px;
            height: 38px;
            background: #FFFFFF;
            border-radius: 6px;
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            color: var(--black-color);
            padding: 6px 115px 6px 10px;
            border: none;
            @include tablet{
                padding: 8px 152px 6px 15px;
            }

            &::placeholder {
                color: #96ACB3;
            }

            &:focus-visible {
                outline: transparent;
            }
        }

        .btn--secondary {
            position: absolute;
            top: 0;
            right: 0;
            z-index: 1;
            max-width: 106px;
            height: 38px;
            font-size: 14px;

            @include tablet{
                max-width: 143px;
                font-size: 16px;
            }
        }
    }
}

.mailbg {
    position: absolute;
    top: 33px;
    left: 21px;
    z-index: 1;
    display: none;

    @include phone{
        
    }

    @include tablet{
        top: 29px;
        left: 29px;
        display: block;
    }
  
}

.mailbg-sm {
    position: absolute;
    z-index: 1;
    width: 142px;
    height: 76px;
    top: -20px;
    right: -60px;
    -webkit-transform: rotate(16deg);
    -ms-transform: rotate(16deg);
    transform: rotate(353deg);

    @include phone{
        right: 90px;
    }

    @include tablet{
        max-width: 143px;
        font-size: 16px;
        display: none;
    }

    @include widescreen{
        top: 121px;
        left: 357px;
        transform: rotate(16deg);
        display: block;
    }
}

