.catalog {

    &__header {
        padding-top: 24px;
        margin-bottom: 16px;

        @include tablet{
            padding-top: 0;
            margin-bottom: 20px;
        }
    }

    &__btns {
        // margin: 0 auto;
        // max-width: 1093px;
        position: relative;
        display: none;

        @include phone{
            display: flex;
            align-items: center;
            margin-bottom: 32px;
        }
        
        @include tablet{
            margin-bottom: 32px;
        }

        @include desktop{
            margin-bottom: 56px;
        }

        .swiper-button {
            top: -2px;
            background-color: var(--white-color);
            z-index: 3;
            flex: 1 0 auto;
            display: none;

            @include tablet{
                display: block;
            }
        }
    }

    &-wrapper {
        display: flex;
        margin-bottom: 160px;
    }

    &-product {
        width: 100%;
        
        &__main {
            width: 100%;
            display: grid;
            grid-template-columns: repeat(2,1fr);
            grid-column-gap: 8px;
            grid-row-gap: 8px;
            margin-bottom: 80px;

            @include phone{
                grid-template-columns: repeat(3,1fr);
                grid-column-gap: 12px;
                grid-row-gap: 16px;
            }

            @include tablet{
                grid-column-gap: 22px;
                grid-row-gap: 20px;
            }

            @include desktop{
                grid-column-gap: 24px;
                grid-row-gap: 40px;
            }

            .product-card {
                padding: 16px 5px;
                // max-width: 257px;

                .product-card__btns {
                    display: flex;
                    justify-content: space-between;

                    .counter-wrapper {
                        .btn--minus,
                        .btn--plus {
                            width: 30px;
                            height: 32px;
                        }

                        .counter {
                            input {
                                width: 30px;
                                height: 32px;
                            }
                        }
                    }

                    .btn--primary {
                        font-size: 0;
                        padding: 0;
                        width: 100%;

                        span {
                            display: block;
                            margin-right: 0;
                            width: 16px;
                            height: 16px;
                        }

                    }

                    @include phone{
                        .btn--primary {
                           
                            span {
                                display: block;
                                margin-right: 0;
                                width: auto;
                                height: auto;
                            }
                        }
                    }

                    @include tablet{

                        .btn--primary {
                            font-size: 16px;
                            padding: 0;
                            width: 100%;
                            margin-left: 10px;
    
                            span {
                                display: none;
                            }
    
                        }

                        .counter-wrapper {
                            .btn--minus,
                            .btn--plus {
                                width: 39px;
                                height: 34px;
                            }
    
                            .counter {
                                input {
                                    width: 39px;
                                    height: 34px;
                                }
                            }
                        }
                    }
                   
                }

                @include tablet{
                    padding: 16px 8px;
                }
                
                @include desktop{
                    padding: 16px 12px;
                }

            }
        }

        &__bors {
            margin-bottom: 80px;
        }
    }

    &__mobile-btns {
        margin-bottom: 24px;
        

        @include phone{
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

        @include widescreen{
            display: none;
        }

        .select {
            width: 100%;

            @include phone{
    
      
              width: 205px;
             
            }
        }

    }

    &__mobile-btns__group {
        display: flex;
        margin-bottom: 10px;

        @include phone{
            margin-bottom: 0;
        }

        .btn--secondary {
            // min-width: 208px;

            @include phone{
                font-size: 14px;
                padding: 11px 11px;
            }

            @include tablet{
                font-size: 16px;
                padding: 11px 35px;
            }

            .btn-icon {
                margin-right: 10px;
                display: block;
                width: 20px;
                height: 20px;
               

                @include phone{
                    flex: 1 0 auto;
                }
            }

            .filters-count {
                padding-left: 5px;
            }
        }

        .btn--reset {
            background-color: transparent;
            color: var(--gray-color);
            min-width: 77px;

            @include phone{
                font-size: 14px;
                padding: 11px 11px;
            }

            @include tablet{
                font-size: 16px;
            }
        }
    }
}

.tabs-list {
    display: flex;
    flex-wrap: wrap;
    gap: 12px;
    margin-bottom: 32px;
    display: none;

    @include widescreen{
        display: flex;
    }

    &__item {
        display: flex;
        align-items: center;
        padding: 3px 6px 2px 8px;
        gap: 10px;
        background: #F6F8FC;
        border-radius: 20px;
        cursor: pointer;
        transition: all .3s ease;


        @include tablet{

            &:hover {
                background-color: #B5C9CE;
                span {
                    color: var(--white-color);
                }

                .tabs-close {
                    svg {
                        fill: var(--white-color);
                    }
                }
       
            }
        }
      
        &.active {
            background-color: #B5C9CE;
            span {
                color: var(--white-color);
            }
           
            .tabs-close {
                svg {
                    fill: var(--white-color);
                }
            }
        }

        span {
            font-weight: 400;
            font-size: 12px;
            line-height: 18px;
            color: #B5C9CE;
        }
    }

    .tabs-close {
        width: 10px;
        height: 10px;

        svg {
            width: 10px;
            height: 10px;
            fill: #D9D9D9;
        }
    }
}


.category-swiper {
    position: relative;
  
    @include phone{
        overflow: visible;
    }
    @include tablet{
        overflow: hidden;
    }

    .swiper-slide {
        flex-shrink: 1;
    }

    &__prev {
        background-image: url("../img/arrow-black.svg");
        background-repeat: no-repeat;
        background-size: 12px 12px;
        background-position: center center;
        transform: rotate(180deg);
        left: -25px;
    }

    &__next {
        background-image: url("../img/arrow-black.svg");
        background-repeat: no-repeat;
        background-size: 12px 12px;
        background-position: center center;
        margin-left: 12px;
        right: -25px;
    }
}


.pagination {

    &__wrapper {
        display: flex;
        justify-content: center;
    }

    &-list {
        display: flex;

        &__item {
            &:not(:first-child) {
                margin-left: 4px;

                @include tablet{
                    margin-left: 8px;
                }
            }

            &:first-child {
                a {
                    background-color: var(--gray4-color);
                    background-image: url("../img/arrow-black.svg");
                    background-repeat: no-repeat;
                    background-size: 6px 12px;
                    background-position: center center;
                    transform: rotate(180deg);
                }
            }

            &:last-child {
                a {
                    background-color: var(--gray4-color);
                    background-image: url("../img/arrow-black.svg");
                    background-repeat: no-repeat;
                    background-size: 6px 12px;
                    background-position: center center;
                }
            }
        }

        &__link {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 40px;
            height: 40px;
            font-weight: 400;
            font-size: 18px;
            line-height: 110%;
            letter-spacing: -0.3px;
            color: var(--gray-semilight-color);
            background-color: transparent;
            border-radius: 6px;

            &.active {
                background-color: var(--green-color);
                color: var(--white-color);
            }
        }
    }
}


.filters {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    z-index: 98;
    height: 100vh;
    overflow-y: scroll;
    transition: left 0.3s ease;
  
    &.filters-visible {
        left: 0;
    }

    @include widescreen{
        min-width: 300px;
        margin-right: 18px;
        position: static;
        width: auto;
        height: auto;
        background-color: transparent;
        z-index: 10;
        overflow: visible;
    }

    &__main {
        width: 100%;
        background-color: #fff;
        position: relative;

        @include tablet{
            max-width: 300px;
        }
    }

    &__close-btn {
        width: 21px;
        height: 21px;

        @include widescreen{
            display: none;
        }
        svg {
            width: 21px;
            height: 21px;
            fill: var(--white-color);
        }
    }

    &__select {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 17px;
        display: none;

        @include widescreen{
            display: flex;
        }

        &-name {
            display: block;
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            color: var(--black-color);
        }

        .select__head {
            height: 33px;
            border: 1px solid #96ACB3;
        }

        .select__list { 
            top: 33px;
            border: 1px solid #D9E4E7;
        }
    }

    &__top {
        display: flex;
        justify-content: space-between;
        padding: 15px 16px 15px 24px;
        background-color: var(--green-color);
        border-bottom: 1px solid var(--gray-light-color);

        @include widescreen{
            padding: 0;
            border-top-left-radius: 6px;
            border-top-right-radius: 6px;
        }

        &-text {
            font-weight: 700;
            font-size: 18px;
            line-height: 115%;
            color: #FFFFFF;
            margin-left: 10px;
            
        }

        &-wrap {
            display: flex;
            align-items: center;

            @include widescreen{
                display: none;
            }
        }
    }

    &__top-link {
        width: 100%;
        padding: 15px 0 15px 24px;
        background-color: var(--green-color);
        border-bottom: 1px solid var(--gray-light-color);
        font-weight: 700;
        font-size: 18px;
        line-height: 115%;
        color: #FFFFFF;
        display: block;
        border-top-left-radius: 6px;
        border-top-right-radius: 6px;
        display: none;

        @include widescreen{
            display: block;
        }
    }

    &__wrapper {
        border: 1px solid var(--gray-light-color);
        border-radius: 6px;
        overflow: hidden;
        padding-bottom: 45px;

        @include widescreen{
            padding-bottom: 0;
            overflow: visible;
        }
    }

    &__item {
        &.acitve {
           
        }
    }

    &__head {
        display: flex;
        align-items: center;
        background-color: var(--gray4-color);
        border-bottom: 1px solid var(--gray-light-color);
        padding: 12px 0 12px 24px;
        position: relative;
        cursor: pointer;

        &-name {
            font-weight: 700;
            font-size: 16px;
            line-height: 110%;
            color: var(--black-color);
            max-width: 198px;
        }

        &:after {
            content: "";
            position: absolute;
            z-index: 1;
            top: 50%;
            right: 1.2rem;
            width: 14px;
            height: 10px;
            transform: translateY(-50%);
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
            transition: all .3s ease;
            background-image: url("../img/arrow-down.svg");
        }

        &.active + .filters__wrap {
            max-height: 1000px;
            margin-bottom: 32px;
            padding-top: 8px;
        }

        &.active.filters__head::after{
            transform: translateY(-50%) rotate(180deg);
        }
      
    }

    &__search {
        margin-bottom: 8px;
    }

    &__body {
        padding-top: 10px;
        max-height: 223px;
        overflow-y: auto;
        width: 100%;
        padding-bottom: 6px;

        &-wrap {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 6px;
            height: 30px;
            &:last-child {
                margin-bottom: 0;
            }
        }

        &-wrapper {
            display: grid;
            grid-template-columns: repeat(3,1fr);
        }
    }

    &__wrap {
        padding: 0px 8px 0 24px;
        max-height: 0;
        overflow: hidden;
        transition: all 0.3s;
        width:100%;
    }

    &__category {
        &-item {
      
            &:not(:last-child) {
                margin-bottom: 12px;
            }

            a {
                font-weight: 400;
                font-size: 14px;
                line-height: 20px;
                color: var(--black-color);
                -webkit-line-clamp: 1;
                -webkit-box-orient: horizontal;
                overflow: hidden;
                -o-text-overflow: ellipsis;
                text-overflow: ellipsis;
                white-space: nowrap;
                width: 93%;
                display: inline-block;
            }

            &.active {
               a {
                font-weight: 700;
                color: var(--green-color);
               }
            }

            li {
                margin-top: 8px;
                padding-left: 8px;
            }
        }

       &-name {
        font-weight: 400;
        font-size: 14px;
        line-height: 100%;
        color: var(--black-color);
       }
    }

    &__checkbox {
        &.custom-checkbox{
          height: 20px;
        }

        .custom-checkbox__text {
            font-weight: 400;
            font-size: 14px;
            line-height: 100%;
            color: var(--black-color);
        }
    }

    &__colors {
        display: flex;
        justify-content: space-between;
        align-items: center;

        &-name {
            display: block;
            font-weight: 400;
            font-size: 14px;
            line-height: 100%;
            color: var(--black-color);
            position: relative;
            padding-left: 26px;

           
        }
    }

    &__buttons {
        display: flex;
        justify-content: center;
        position: fixed;
        bottom: 0;
        left: -100%;
        z-index: 10;
        transition: left 0.3s ease;
        width: 100%;

        @include widescreen{
            position: static;
            margin-top: 24px;
            .btn--reset {
                margin-left: 8px;
            }
        }

        &.fixed {
            left: 0;
            background-color: var(--green-color);
            z-index: 10;
            padding: 10px;

            @include tablet{
                width: 300px;
            }

            .btn--secondary {
                width: 141px;
                background-color: var(--white-color);
                color: var(--black-color);

                @include tablet{
                    padding: 11px 30px;
                }
            }
            
            .btn--reset {
                width: 129px;
                margin-left: 8px;
                background-color: transparent;
                border: 1px solid var( --gray-light-color);
                color: var(--gray-light-color);

                @include tablet{
                    padding: 11px 31px;
                }
            }
        } 
       
    }

    &__radio {
        display: flex;
        align-items: center;
        &:not(:last-child) {
            margin-bottom: 10px;
        }

        &-name {
            font-weight: 400;
            font-size: 14px;
            line-height: 100%;
            color: var(--black-color);
        }
    }
    
}

