.range {
    margin-bottom: 18px;
    width: 97%;
    position: relative;
    left: 9px;

    @include tablet{
        width: 94%;
    }

    .noUi-handle {
       border: none;
       border-radius: 100%;
       background: var(--green-color);
       cursor: pointer;
       box-shadow: none;
    }

    .noUi-target {
        background: var( --gray-light-color);
        border-radius: 5px;
        border: transparent;
        box-shadow: none;
    }

    &.noUi-horizontal {
        height: 4px !important;
    }


   .noUi-handle:before, .noUi-handle:after {
    content: "";
    display: none;
    position: absolute;
    height: 14px;
    width: 1px;
    background: #E8E7E6;
    left: 14px;
    top: 6px;
    }

    .noUi-handle {
        width: 16px;
        height: 16px;
        right: -8px;
        top: -6px;
    }

    .noUi-handle:after {
       content: "";
       display: none;
       position: absolute;
       height: 14px;
       width: 1px;
       background: #E8E7E6;
       left: 14px;
       top: 6px;
   }

   .noUi-base, .noUi-connects {
       width: 100%;
       height: 4px;
       position: relative;
       z-index: 1;
       background: var(--gray-light-color);
       border-radius: 5px;
       top: -1px;
   }

   .noUi-connect {
        background: var(--green-color);
        border-radius: 5px;
       height: 4px;
   }

   .noUi-touch-area {
        height: 100%;
        width: 100%;
        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 1;
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background-color: #fff;
        }
    }

}

.filter-price {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;

    &__label {
        display: flex;
        align-items: center;
        border: 1px solid var(--gray-light-color);
        border-radius: 2px;
        width: 100%;
        padding: 12px 14px;
        &:not(:first-child) {
            margin-left: 16px;
        }
    }

    .filter-price__text {
        font-weight: 700;
        font-size: 14px;
        line-height: 100%;
        color: var(--black-color);
    }

    &__input {
        width: 100% !important;
        font-weight: 400;
        font-size: 14px;
        line-height: 100%;
        color: var(--black-color);
        padding-left: 6px;
        &:focus-visible {
            outline: none;
        }

    }
}


.radio {
   
    input[type="radio"] {
      position: absolute;
      opacity: 0;

      + .radio-label {
        position: relative;
        font-weight: 400;
        font-size: 14px;
        line-height: 100%;
        color: #133D4D;

        &:before {
          content: '';
          background: transparent;
          border-radius: 100%;
          border: 2px solid #96ACB3;
          display: inline-block;
          width: 16px;
          height: 16px;
          position: relative;
          top: -2px;
          margin-right: 10px; 
          vertical-align: top;
          cursor: pointer;
          text-align: center;
         
        }

        &:after {
            content: '';
            position: absolute;
            left: 4px;
            top: 5px;
            z-index: 1;
            background: #fff;
            border-radius: 100%;
            border: none;
            width: 8px;
            height: 8px;
            cursor: pointer;
        }
          
      }
      &:checked {
        + .radio-label {
          &:before {
            background: #fff;
            background-color: var(--green-color);
            border: transparent;
          }
        }
      }
      &:focus {
        + .radio-label {
          &:before {
            outline: none;
            border-color: none;
          }
        }
      }
    }
}


.switcher {
    display: block;
  
    &__btn {
      cursor: pointer;
      display: block;
      position: relative;
      width: 40px;
      height: 22px;
      border-radius: 30px;
      transition: all .3s;
      background: #F5F5F5;
  
      &:after {
        content: '';
        position: absolute;
        top: calc(50% - 8px);
        left: 4.5px;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background-color: #fff;
        transition: all .3s;
        border: none;
      }
    }
  
    &__input:checked + .switcher__btn {
      background-color: var(--green-color);
      &:after {
        transform: translateX(16px);
      }
    }
}

.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0,0,0,0);
    border: 0;
}


.watched {
    &__swiper {

        @include tablet {
            min-height: 466px;
        }
    }
}

.bor {
    background-color: var(--white-color);
    border: 1px solid var(--gray-light-color);
    border-radius: 6px;

    &:not(:last-child) {
        margin-bottom: 24px;
    }

    &__top {
        display: flex;
        padding: 13px 14px 0 0px;
        align-items: end;

        @include phone{
            padding: 13px 8px 0 0px;
            align-items: center;
        }

        @include tablet{
            padding: 13px 16px 0 16px;
            margin-bottom: 28px;
            align-items: flex-start;
        }

        &-head {
            margin-bottom: 20px;

            @include tablet{
                margin-bottom: 28px;
            }
        }

        &-bottom {
            display: flex;
            align-items: center;
            justify-content: space-between;
           

            @include tablet{
                max-width: 367px;
            }
        }

        &-left {
            width: 64px;
            height: 65px;
            transform: rotate(270deg);

            @include phone{
                width: 71px;
                height: 48px;
            }

            @include tablet{
                width: 136px;
                height: 65px;
                transform: none;
            }
    
        }

        &-right {
            padding-bottom: 16px;

            @include tablet{
                padding-bottom: 0px;
            }
        }
    }

    &__img {
        width: 147px;
        height: 100%;

        @include phone{
            width: 108px;
        }

        @include tablet{
            width: 136px;
            height: 54px;
        }

        @include desktop{
            height: 65px;
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
        }
    }

    &__name {
        font-weight: 700;
        font-size: 12px;
        line-height: 120%;
        color: var(--black-color);

        @include phone{
            font-size: 14px;
        }
    }

    &__price {
        display: block;
        font-weight: 700;
        font-size: 20px;
        line-height: 100%;
        color: #063A4B;

        @include desktop{
            min-width: 150px;
        }
       
    }

    &__btns  {
        display: flex;
        align-items: center;
        border: none !important;
        .btn--primary {
            padding: 9px 29px;
           
            @include phone{
                padding: 9px 12px;
            }

            @include tablet{
                margin-left: 25px;
            }

            @include desktop{
                margin-left: 27px;
            }
        }

        .btn--minus {
            display: none;

            @include phone{
                display: flex;
            }
         
        }

        .btn--plus {
            display: none;
            @include phone{
                display: flex;
            }
        }
    }

    &__counter {
        position: relative;
        display: none;
        @include phone{
            display: block;
        }
    }

    &__bottom {
        background: var(--gray4-color);
        border-radius: 4px;
        padding: 4px 0px;
        display: grid;
        grid-template-columns: repeat(1,1fr);
        font-weight: 400;
        font-size: 12px;
        line-height: 18px;
        color: #133D4D;

        @include phone{
            grid-template-columns: repeat(12,1fr);
            padding: 12px 8px;
        }

        @include tablet{
         
            padding: 10px 16px;
        }

        &-item {
            &:nth-of-type(1) {
               
                grid-column: 1/1;
        

                @include phone{
                   
                    grid-column: 8 span;
                    grid-row: 1/1;
                
                }

                @include tablet{
                    grid-column: 2 span;
                    grid-row: auto;
                }
            }

            &:nth-of-type(2) {
              
                display: none;

                @include phone{
                   display: block;
                   grid-column: 8 span;
                    grid-row: 2/2;
                }

                .bor__long-bor {
                    display: none;

                    @include phone{
                        display: block;
                    }

                    @include tablet{
                        display: none;
                    }
                }

                @include tablet{
                    grid-column: 5 span;
                    grid-row: auto;
                    padding-left: 6px;
                }
            }

            &:nth-of-type(3) {
                grid-column: 3 span;
                display: none;

               
                @include phone{
                   display: block;
                   grid-column: 4 span;
                }

                @include tablet{
                    grid-column: 2 span;
                }

                @include desktop{
                    grid-column: 3 span;
                }

                .bor__long-bor {
                    display: none;

                    @include tablet{
                        display: block;
                    }
                }
            }

            &:nth-of-type(4) {
                grid-column: 2 span;
                display: none;

                @include phone{
                    display: block;
                    grid-column: 4 span;
                }

                @include tablet{
                    grid-column: 3 span;
                }

                @include desktop{
                    grid-column: 2 span;
                }
            }

            span {
                display: block;
            }
        }
    }

    &__marking {
        position: relative;
        max-width: 90px;
        left: 62px;

        @include phone{
            left: 0;
        }

        &.colors-green::before {
            right: 0;
            left: auto;
        }
    }
}

.counter-wrapper {
    border: 1px solid #F5F5F5;
    border-radius: 4px;
    display: flex;
    align-items: center;

    .btn--primary {
        padding: 9px 29px;
    }

    .btn--minus {
        &::before {
            content: "";
            background-image: url("../img/icon_minus.svg");
            background-repeat: no-repeat;
            background-position: center center;
            background-size: cover;
            width: 15px;
            height: 2px;
        }
    }

    .btn--plus {
        &::before {
            content: "";
            background-image: url("../img/icon_plus.svg");
            background-repeat: no-repeat;
            background-position: center center;
            background-size: cover;
            width: 14px;
            height: 12px;
        }
    }
}

.counter {
    position: relative;
    input {
        border: none !important;
        text-align: center;
        width: 39px;
        height: 34px;
        font-weight: 400;
        font-size: 18px;
        line-height: 90%;
        letter-spacing: -0.3px;
        color: #133D4D;
        position: relative;
        top: 0px;
        padding: 2px 0px !important;
        outline: none !important;
       }

       &::after {
        content: '';
        position: absolute;
        top: 0px;
        left: 0;
        z-index: 1;
        width: 1px;
        height: 100%;
        background-color: #D9E4E7;
       }

       &::before {
        content: '';
        position: absolute;
        top: 0px;
        right: 0;
        z-index: 1;
        width: 1px;
        height: 100%;
        background-color: #D9E4E7;
       }
}