.breadcrumbs-wrapper {
    padding-top: 24px;
    padding-bottom: 24px;
    display: none;

    @include tablet {
        display: block;
    }
}

.breadcrumbs {
    display: flex;
    &__item {
        position: relative;

        &:not(:first-child) {
            margin-left: 21px;
        }

        &:not(:last-child) {
            &::after {
                content: "/";
                position: absolute;
                top: 0;
                right: -12px;
                z-index: 1;
                font-weight: 400;
                font-size: 12px;
                line-height: 18px;
                color: #96ACB3;
            }
        }
    }

    &__link {
        font-weight: 400;
        font-size: 12px;
        line-height: 18px;
        color: var(--gray-color);
    }
}