.search {
    padding-top: 24px;
    padding-bottom: 120px;

    @include tablet {
        padding-top: 32px;
        padding-bottom: 160px;
    }

    &__header {
        margin-bottom: 24px;

        @include tablet {
            margin-bottom: 56px;
        }

        .btn--secondary {
            max-width: 180px;
            padding: 11px 24px;
        }
    }

    .catalog-product__main {
      
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 8px;
        grid-row-gap: 8px;

        @include phone {
            grid-template-columns: repeat(3, 1fr);
            grid-column-gap: 12px;
            grid-row-gap: 16px;
        }

        @include tablet {
            grid-column-gap: 22px;
            grid-row-gap: 20px;
        }

        @include widescreen {
            grid-template-columns: repeat(4, 1fr);
            grid-column-gap: 40px;
            grid-row-gap: 40px;
        }
    }

    &__title-notfound {
        margin-bottom: 18px;

        @include tablet {
            margin-bottom: 14px;
        }
    }

    &__subtitle {
        display: block;
        font-weight: 400;
        font-size: 14px;
        line-height: 115%;
        color: var(--black-color);
        margin-bottom: 24px;

        @include tablet {
            font-size: 18px;
            margin-bottom: 32px;
        }
    }
}