.order-success {
    padding-top: 24px;
    padding-bottom: 120px;

    @include tablet {
        padding-top: 32px;
        padding-bottom: 136px;
    }

    &__header {
        margin-bottom: 24px;

        @include tablet {
            margin-bottom: 32px;
        }
    }

    &__wrapper {
        max-width: 760px;
        margin-bottom: 64px;
    }

    &__item {
        &:not(:last-child) {
            margin-bottom: 25px;
        }

        @include tablet {
            &:not(:last-child) {
                margin-bottom: 35px;
            }
        }
    }

    &__text {
        font-weight: 400;
        font-size: 14px;
        line-height: 24px;
        color: var(--black-color);

        @include tablet {
            font-size: 16px;
        }

        span {
            font-weight: 700;
        }

        a {
            font-weight: 700;
            color: var(--black-color);
        }
    }

    .btn--secondary {
        max-width: 354px;
        margin-bottom: 12px;
    }

    &__memo {
        display: block;
        font-weight: 400;
        font-size: 16px;
        line-height: 100%;
        color: var(--gray-color);
    }
}