.order {
    padding-top: 24px;
    margin-bottom: 120px;

    @include widescreen {
        padding-top: 32px;
        margin-bottom: 136px;
    }

    &__header {
        margin-bottom: 32px;
        flex-direction: column;

        .section-header__wrap {
            margin-bottom: 16px;

            @include phone {
                margin-bottom: 0px;
            }
        }

        .btn--transparent {
            max-width: 100%;
        }

        @include phone {
            flex-direction: row;
            .btn--transparent {
                max-width: 160px;
            }
        }
        
    }

    &__title {
        font-weight: 700;
        font-size: 18px;
        line-height: 115%;
        color: var(--black-color);
        margin-bottom: 24px;

        @include tablet {
            font-size: 22px;
            line-height: 120%;
        }

        &--comment {
            margin-bottom: 6px;
        }
    }

    &__wrapper {
      
        @include widescreen {
            display: flex;
            justify-content: space-between;
            position: relative;
        }
    }

    &__left {
        @include tablet {
            max-width: 543px;
        }
    
        @include widescreen {
            min-width: 460px;
        }
    }

    &__right {

        @include widescreen {
            min-width: 460px;
        }
    }

    &__info {
        .btn--secondary {
            margin-bottom: 0;
        }
    }

    &__wrap {

        &:not(:last-child) {
            margin-bottom: 64px;
        }
        @include tablet {
           
            &:not(:last-child) {
                margin-bottom: 80px;
            }
        }
    }

    &__buyer {

        &:not(:last-child) {

        }

        .select--order  {
            width: 100%;
            font-size: 14px;
            line-height: 115%;

            @include tablet {
                font-size: 16px;
            }
        }

        .select__head {
            border: 1px solid var(--gray-color);

            padding: 7px 0px 7px 8px;
            height: 32px;

            @include tablet {
                padding: 6px 0px 6px 8px;
                height: 41px;
            }

            &:after {
                right: 9px;
            }

            .select__text {
                
                white-space: nowrap;
                -webkit-line-clamp: 1;
                -webkit-box-orient: horizontal;
                overflow: hidden;
                -o-text-overflow: ellipsis;
                text-overflow: ellipsis;
                width: 90%;

                @include phone {
                    display: -webkit-box;
                    width: 100%;
                }
            }
           
        }

        .form__group {
            margin-bottom: 22px;

            @include tablet {
                margin-bottom: 30px;
            }
        }

        .form__name {
            font-size: 12px;

            @include tablet {
                font-size: 14px;
            }
        }

        .form__input {
            padding: 6px 8px 6px 8px;
            height: 32px;
            border: 1px solid var(--gray-color);

            @include tablet {
                height: 41px;
                padding: 12px 14px 12px 14px;
            }
           
        }

        .form__textarea {
            height: 89px;
            font-size: 12px;
            border: 1px solid var(--gray-color);

            @include tablet {
                font-size: 14px;
                height: 140px;
            }

        }
    }   

}

.counter-symbol {
    display: block;
    margin-top: 6px;
    font-weight: 400;
    font-size: 12px;
    line-height: 140%;
    color: var(--gray-color);
}


.stick-top {
    position: fixed;
    top: 83px;
    right: calc((100% - var(--content-width))/ 2);
    width: 460px;
}

