// }

.order-delivery {
    padding-top: 14px;
    padding-bottom: 0px;

    @include tablet {
        padding-top: 22px;
        padding-bottom: 12px;
    }

    
    &__item {
        display: none;
        min-height: 135px;

        &.active {
            display: block;
        }
    }

    &__price {
        display: block;
        font-weight: 700;
        font-size: 14px;
        line-height: 115%;
        color: var(--black-color);
        margin-bottom: 11px;
    }

    &__text {
        display: block;
        font-weight: 400;
        font-size: 14px;
        line-height: 115%;
        color: var(--black-color);
    }

}

.map-order {
    width: 100%;
    height: 261px;

    margin-top: 24px;

    @include tablet {
        margin-top: 44px;
    }
}