.special {
  padding-top: 80px;
  padding-bottom: 80px;

  @include widescreen {
    padding-top: 118px;
    padding-bottom: 128px;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__swiper {
    margin-bottom: 80px;

    @include desktop {
      min-height: 388px;
    }

    @include widescreen {
      min-height: 466px;

    }

    .swiper-slide {
      max-width: 290px !important;
    }

    .product-card__shields {
      display: none;
    }

    // .product-card__btns {
    //     max-width: 142px;
    // }

    // .product-card {
    //     padding: 16px 14px;
    // }
  }

  &__banners {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-row-gap: 20px;

    @include desktop {
      grid-row-gap: 0;
      grid-column-gap: 40px;
      grid-template-columns: repeat(12, 1fr);
    }
  }

  &__banner {
    display: flex;
    align-items: center;
    background-color: #f1ffba;
    border-radius: 12px;
    padding: 0px 0px 0px 8px;
    position: relative;
    grid-column: 6 span;
    justify-content: space-between;

    @include phone {
      padding: 0px 0px 0px 12px;
    }

    @include tablet {
      padding: 0px 0px 0px 32px;
    }

    &-img {
      width: 134px;
      height: 116px;
      flex-shrink: 0;
      
      @include tablet {
        width: 139px;
        height: 120px;
      }

    
      @include widescreen {
        width: 191px;
        height: 164px;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }

    }

    &--blue {
      background-color: #dbf8ff;
    }

    &-name {
      font-weight: 700;
      font-size: 16px;
      line-height: 120%;
      color: var(--black-color);
      max-width: 152px;


      @include phone {
        font-size: 18px;
        max-width: 260px;
      }

      @include tablet {
        font-size: 22px;
        max-width: 320px;
      }

      @include widescreen {
        font-size: 22px;
        max-width: 340px;
      }
    }

    &-img {
    }
  }
}

.product-card {
  background-color: var(--white-color);
  border: 1px solid var(--gray4-color);
  padding: 5px 5px 16px 5px;
  // max-width: 290px;

  @include widescreen {
    padding: 16px 16px;
  }

  @include desktop{
    &:hover {
      .product-card__btns {
        display: flex;
      }
    }
  }

  &--notavalible {
   .product-card__img {
    opacity: 0.5;
   }

    .product-card__btns {
      .btn--thirdly {

        display: flex;
      }
    }

   .btn--primary {
      display: none;
    }

    .counter-wrapper {
      display: none;
    }

  }

  &__top {
    margin-bottom: 12px;
    position: relative;

    @include widescreen {
      margin-bottom: 16px;
    }

    &.minus {
      .shield-minus {
        display: flex;
      }
    }
  }

  &__img {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 133px;

    @include phone {
      height: 212px;
    }

    @include widescreen {
      height: 250px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &-bottom {
  }

  &__name {
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    font-weight: 400;
    font-size: 12px;
    line-height: 115%;
    color: var(--black-color);
    margin-bottom: 12px;
    min-height: 55px;

    @include phone {
      font-size: 14px;
      -webkit-line-clamp: 3;
      min-height: 50px;
      margin-bottom: 16px;
    }

    @include widescreen {
      font-size: 18px;
      min-height: 62px;
    }
  }

  &__price {
    margin-bottom: 12px;

    @include phone {
      margin-bottom: 16px;
      display: flex;
      flex-direction: column;
    }

    @include tablet {
      margin-bottom: 16px;
      display: flex;
      align-items: baseline;
      flex-direction: row;
    }

    &-current {
      display: block;
      font-weight: 700;
      font-size: 16px;
      line-height: 95%;
      color: var(--black-color);
      margin-bottom: 4px;

      @include phone {
        font-size: 20px;
       
      }

      @include tablet {
        margin-bottom: 0;
      }

      @include widescreen {
        font-size: 24px;
        line-height: 100%;
       
      }
    }

    &-old {
      font-weight: 400;
      font-size: 12px;
      line-height: 100%;
      text-decoration-line: line-through;
      color: var(--gray-semi-color);
      display: block;

      @include phone {
        font-size: 14px;
        margin-left: 4px;
      }

      @include widescreen {
        margin-left: 8px;
      }
    }
  }

  &__btns {

    display: flex;

    @include desktop {
      display: none;
    }

      .counter-wrapper {
        .btn--minus,
        .btn--plus {
            width: 30px;
            height: 32px;
        }

        .counter {
            input {
                width: 30px;
                height: 32px;
            }
        }
    }

    .btn--primary {
        font-size: 0;
        padding: 0;
        width: 100%;
       

        span {
            display: block;
            margin-right: 0;
            width: 20px;
            height: 20px;
        }

    }

  @include phone{
      .btn--primary {
        margin-left: 10px;
          span {
              display: block;
              margin-right: 0;
              width: auto;
              height: auto;
          }
      }
  }

  @include tablet{

      .btn--primary {
          font-size: 16px;
          padding: 0px;
          width: 100%;
          span {
              display: none;
          }

      }

      .counter-wrapper {
          .btn--minus,
          .btn--plus {
              width: 36px;
              height: 34px;
          }

          .counter {
              input {
                  width: 34px;
                  height: 34px;
              }
          }
      }
  }

  @include widescreen{
    .counter-wrapper {
      .btn--minus,
      .btn--plus {
          width: 39px;
          height: 34px;
      }

      .counter {
          input {
              width: 39px;
              height: 34px;
          }
      }
  }
  }

  .btn--thirdly {
      display: none;
    }
  }

  &__shields {
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 1;
    display: none;

    @include tablet {
      display: flex;
    }

    &.points-10 {
      .shield-points-10 {
        display: flex;
      }
    }

    &.points-30 {
      .shield-points-30 {
        display: flex;
      }
    }

    &.points-70 {
      .shield-points-70 {
        display: flex;
      }
    }

    &.new {
      .shield-new {
        display: flex;
      }
    }

    &.sale {
      .shield-sale {
        display: flex;
      }
    }

    &.points {
      .shield-points {
        display: flex;
      }
    }

    &.not {
      .shield-notavalible {
        display: flex;
      }
    }
  }
}

.shield {
  display: none;
  align-items: center;
  justify-content: center;
  font-weight: 400;
  font-size: 12px;
  line-height: 120%;
  color: var(--black-color);
  border-radius: 6px;
  padding: 3px 6px 1px 6px;

  &:not(:last-child) {
    margin-right: 6px;
  }

  &-points-10 {
    background-color: #eef5d1;
  }
  &-points-30 {
    background-color: #f5e7d1;
  }
  &-points-70 {
    background-color: #f5d7d1;
  }
  &-new {
    background-color: #d1efff;
  }
  &-sale {
    background-color: #fde1d2;
  }
  &-points {
    background-color: transparent;
    border: 1px solid var(--orange-color);
    color: var(--orange-color);
  }
  &-notavalible {
    background-color: #d9e4e7;
    color: var(--gray-color);
  }

  &-minus {
    position: absolute;
    top: 0%;
    left: 0;
    z-index: 1;
    border: 1px solid var(--gray-color);
    color: var(--gray-color);
    display: none;
  }
}
