.header {
    position: relative;
    
    background-color: var(--white-color);

    &__fixed {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 98;
        

        .header__top {
            display: none;
        }

        .header__bottom {
            @include tablet {
                padding-top: 15px;
                padding-bottom: 15px;
            }
        }

        .header__bottom-wrap {
            margin-bottom: 0px;
        }

        .header__search-mobile {
            display: none;
        }

        .menu--active {
            top: 77px;
            height: calc(100vh - 77px);
        }
        
    }

    &__top {
        position: absolute;
        top: 97px;
        left: -100%;
        z-index: 30;
        width: 100%;
        height: calc(100vh - 97px);
        background: #fff;
        transition:  all .3s ease;
        @include tablet {
            position: static;
            height: auto;
            z-index: 1;
        }

        &.active {
            left: 0;
        }
        
       &-wrap {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 20px 16px;
        height: 100%;

            @include tablet {
                flex-direction: row;
                align-items: center;
                padding: 17.5px 0 20.5px 0;
            }
       }
    }

    &__logo {
        display: block;
        min-width: 113px;

        @include tablet {
            min-width: 146px;
        }

        @include desktop {
            min-width: 200px;
        }
    }

    &__bottom {
        padding-top: 10px;
        padding-bottom: 10px;
        

        @include tablet {
            padding-top: 0px;
            padding-bottom: 0px;
        }

        &-wrap {
            position: relative;
            display: flex;
            align-items: center;
            margin-bottom: 10px;

            @include tablet {
                margin-bottom: 0px;
            }
        }
    }

    &__wrap {
        width: 100%;
        display: flex;
        align-items: center;
        // margin-left: 38px;

        @include tablet {
             margin-left: 16px;
        }

        .form__search {
            display: none;

            @include tablet {
               display: block;
            }
        }
    }

    &__links {
        &-phone {
            font-weight: 400;
            font-size: 12px;
            line-height: 18px;
            color: var(--gray-color);
    
            @include widescreen {
                font-size: 14px;
                line-height: 20px;
            }
        }

        &-popup {
            font-weight: 400;
            font-size: 14px;
            line-height: 21px;
            color: var(--green-color);
            position: relative;
            margin-left: 26px;

            @include widescreen {
                font-size: 16px;
                line-height: 24px;
                margin-left: 18px;
            }

            &::after {
                content: "";
                position: absolute;
                bottom: 3px;
                left: 0;
                z-index: 1;
                width: 100%;
                height: 1px;
                background-color: var(--green-color);
            }
        }
    }

    &__options {
        padding-right: 53px;

        @include tablet {
            margin-left: 25px;
            padding-right: 0px;
        }

        @include widescreen {
            margin-left: 57px;
        }
    }


    &__search-mobile {
      
        @include tablet {
            display: none;
        }
    }
}

.nav {
    &__list {
        display: flex;
        flex-direction: column;
        @include tablet {
            align-items: center;
            flex-direction: row;
        }
    }

    &__item {

        &:not(:last-child) {
            margin-bottom: 22px;
        }

        @include tablet {
            align-items: center;
            

            &:not(:first-child) {
                margin-left: 14px;
                @include widescreen {
                    margin-left: 32px;
                }
            }

            &:not(:last-child) {
                margin-bottom: 0;
            }
        }
       
    }

    &__link {
        font-weight: 400;
        font-size: 16px;
        line-height: 115%;
        color: var(--black-color);

        @include tablet {
            font-size: 12px;
            line-height: 18px;
            color: var(--gray-color);
        }
      

        @include widescreen {
            font-size: 14px;
            line-height: 20px;
        }
    }
}

.btn-catalog {
    
    font-weight: 400;
    font-size: 16px;
    line-height: 115%;
    color: #133D4D;

    @include tablet {
       display: none;
    }
}


.options {
    display: flex;
    align-items: center; 
  
    &__item {
        display: flex;
        align-items: center;
        position: relative;

        &:not(:last-child) {
            margin-right: 14px;

            @include tablet {
                margin-right: 20px;
            }

            @include widescreen {
                margin-right: 34px;
            }

            &::after {
                content: "";
                position: absolute;
                right: -23px;
                top: 0px;
                z-index: 1;
                width: 1px;
                height: 38px;
                background-color: var(--gray-light-color);
                display: none;

                @include tablet {
                    display: block;
                    right: -15px;
                }
            }
        }
    }

    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 19px;
        height: 19px;
        position: relative;

        img {
            max-width: none;
            width: 19px;
            height: 19px;  
        }

        @include tablet {
            width: 32px;
            height: 32px;

            img {
                max-width: none;
                width: 22px;
                height: 22px   
            }
        }

      
    }

    &__counter {
        position: absolute;
        right: -11px;
        bottom: -3px;
        z-index: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: var(--orange-color);
        font-weight: 700;
        font-size: 7px;
        line-height: 80%;
        color: var(--white-color);
        border-radius: 60px;
        padding: 3px 5px;

        @include tablet {
            right: -6px;
            bottom: 1px;
            padding: 4px 5px 2px 5px;
        }
    }

    &__summ {
        margin-left: 20px;
        display: none;

        @include desktop {
            display: block;
        }
        
        &-name {
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            color: var(--gray-color);
        }

        &-sum {
            font-weight: 700;
            font-size: 14px;
            line-height: 20px;
            color: var(--black-color);
            display: block;
            min-width: 68px;
        }
    }
}


.burger {
    @include burger;
}

.menu {
    &--active {
    position: absolute;
      top: 111px;
      left: 0;
      right: 0;
      width: 100%;
      background-color: rgba(0, 0, 0, 0.6);
      transition: all 0.5s ease;
      height: calc(100vh - 111px);
      z-index: 100;
      overflow-y: auto;
      .menu__desktop {
        left: 0;
      }
    }

    &__desktop {
        position: absolute;

        left: -100%;
        z-index: 101;
        opacity: 1;
        width: 100%;
        transition: all .3s ease-in;
    }

    &__wrapper {
        overflow: hidden;
        padding: 24px 24px;
        background-color: white;
        border: 1px solid #D9E4E7;
        height: 644px;
    }

    &__item {
        max-width: 350px;
        position: relative;
        height: 100%;
        &-wrap {
            height: 100%;
            overflow-y: auto;
            &::-webkit-scrollbar {
                width: 5px;
                background-color: #F6F8FC;
            }
              
              &::-webkit-scrollbar-thumb {
                background: #D9E4E7;
                border-radius: 12px;
            }
        }
    }
  

    &__list {
        padding-right: 18px;
        span {
            display: block;
            max-width: 277px;
        }

        li {
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-weight: 400;
            font-size: 18px;
            line-height: 115%;
            color: var(--black-color);
          

            &:not(:last-child) {
                margin-bottom: 20px;
            }
        }

        &-item {
            
            &.active {
                .menu__list-link {
                    color: var(--orange-color);
                }
            }
            a {
                font-weight: 400;
                font-size: 18px;
                line-height: 115%;
                color: var(--black-color);
            }
        }

    }

    &__dropdown {
        padding-right: 18px;
        &-item {
         display: flex;
         flex-direction: column;
         align-items: flex-start;
          &.active {
            .menu__dropdown-link {
                color: var(--orange-color);
            }

            .menu__right-wrap {
                margin-bottom: 10px;
            }
          }
        }

        &-btn {
            transition: all .3s ease;
            &.active {
                .menu__chevrone {
                  
                    fill: var(--orange-color);
                    transform: rotate(90deg);
                }
            }   
        }
    }


    &__btn {
    
        &.active {
            .menu__chevrone {
                fill: var(--orange-color);
            }
        }
    }

    &__chevrone {
        width: 10px;
        height: 10px;
        fill: var(--black-color);
        transition: all .3s ease;
    }

    &__right {
        position: absolute;
        top: 0;
        left: 110%;
        z-index: 1;
        min-width: 350px;
        height: 100%;
        opacity: 0;
        visibility: hidden;
        transition: all .3s ease-in-out;

        &.active {
            opacity: 1;
            visibility: visible;
        }

        &-wrap {
            width: 100%;
            display: flex;
            justify-content: space-between;
        }

        &-wrapper {
            overflow-y: auto;
            height: 100%;
            &::-webkit-scrollbar {
                width: 5px;
                background-color: #F6F8FC;
            }
              
              &::-webkit-scrollbar-thumb {
                background: #D9E4E7;
                border-radius: 12px;
            }
        }
    }

}

.overlay-filter {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 100;
    //z-index: -1;
    background-color: rgba(0, 0, 0, 0.6);
    transition: opacity 0.5s ease;
}


.dropdown-list {
    width: 100%;
    padding: 12px 0 12px 34px;
    background: #F6F8FC; 
    display: none;

    &.active {
        display: block;
    }

    li {
        &:not(:last-child) {
            margin-bottom: 12px;
        }
    }
}