//     sass- -  root
// colors
:root {
  // base
  --font-family: "Circe", sans-serif;
  --content-width: 1130px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --white-color: #fff;
  --black-color: #133D4D;

  --green-color: #10DBB6;
  --green-hover-color: #10C9A8;
  --green-active-color: #3DBDA6;

  --orange-color: #FF6F44;
  --orange-hover-color: #EB653E;
  --orange-active-color: #E16F4D;

  --gray-color: #96ACB3; //gray1
  --gray-semi-color: #B5C9CE; //gray2
  --gray-semilight-color: #D9E4E7; //gray3
  --gray4-color: #F6F8FC; //gray4
  --gray-light-color: #F5F5F5; //gray5


  --base-transition: all .3s ease;

  --hero-slider-speed: 500;
}
