.notavailable {
    padding-bottom: 120px;

    &__wrapper {
        border: 1px solid var(--gray-light-color);
    }

    &__item {
        display: grid;
        grid-template-columns: repeat(4,1fr);
        align-items: center;
        padding: 10px 10px;
        position: relative;
        padding-bottom: 12px;

        @include tablet {
            padding-top: 0px;
            padding-bottom: 120px;
            grid-template-columns: repeat(12,1fr);
            align-items: center;
            padding: 10px 0;
            padding-bottom: 10px;
        }

        &:not(:last-child) {
            border-bottom: 1px solid var(--gray-light-color);
        }

        .btn--delete {
            bottom: 20px;
            right: 11px;

            @include phone {
                bottom: 25px;
            }

            @include tablet {
             right: 24px;
             bottom: 10px;
            }
        }
    }


    &__table {

    }

    &__head {
       &-item {
        &:nth-child(1) {
            @include widescreen {
                    grid-column: 6/8;
            }
        }

        &:nth-child(2) {
            @include widescreen {
                    grid-column: 8/10;
            }
        }
       }
    }

    &__img {
        grid-column: 4 span;
        height: 230px;

        @include tablet {
            grid-column: 2 span;
            height: 116px;
        }

        img {
            opacity: 0.5;
        }
    }

    &__info {
        grid-column:4 span;
        margin-bottom: 14px;
        
        @include tablet {
            grid-column: 3 span;
            margin-bottom: 0px;
        }
    }

    &__name {
        display: block;
        color: var(--gray-color);
        margin-bottom: 0;
    }

    &__maxdiscount {
        display: block;
        grid-column: 4 span;
        text-align: left;
        color: var(--gray-color);
        span {
            padding: 0;
        }

        @include tablet {
            grid-column: 2 span;
            text-align: center;
            margin-bottom: 0px;
          
        }
    }

    &__price {
        display: block;
        grid-column: 4 span;
        grid-row: 4/4;
        text-align: left;
        color: #96ACB3;
        margin-bottom: 22px;

        @include tablet {
            grid-column: 2 span;
            text-align: center;
            grid-row: auto;
            margin-bottom: 0px;
        }
    }
    
    &__btn {
        grid-column: 4 span;

        @include tablet {
            grid-column: 10/13;
        }

        .btn--white {
            max-width: 166px;
            font-size: 14px;
            padding: 8px 0px;

            @include phone {
                max-width: 260px;
                font-size: 16px;
                padding: 14px 0px;
            }
          
        }

        
    }
}