/* stylelint-disable */

@mixin burger {
  --burger-width: 21px;
  --burger-height: 2px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  z-index: 1;
  border: none;
  padding: 0;
  width: var(--burger-width);
  color: var(--black-color);
  background-color: var(--green-color);
  cursor: pointer;
  padding: 6.5px 6.5px;
  width: 34px;
  height: 34px;
  border-radius: 4px;

  @include tablet {
    position: relative;
    width: 145px;
    height: 38px;
    padding: 12px 25px 12px 28.5px;
    top: auto;
    transform: none;
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 6.5px;
    width: 21px;
    height: 2px;
    background-color: #fff;
    transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    border-radius: 4px;

    @include tablet {
      left: 28.5px;
    }
  }

  &::before {
    top: 11px;
  }

  &::after {
    bottom: 11px;
  }

  &__line {
    position: absolute;
    top: 50%;
    left: 6.5px;
    width: 21px;
    height: 2px;
    background-color: #fff;
    transform: translateY(-50%);
    transition: transform 0.3s ease-in-out;
    border-radius: 4px;

    @include tablet {
      left: 28.5px;
    }
  }

  &--active {
    &::before {
      top: 46%;
      transform: rotate(45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    &::after {
      top: 46%;
      transform: rotate(-45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    .burger__line {
      transform: scale(0);
      transition: transform 0.3s ease-in-out;
    }
  }

  &__name {
    font-weight: 700;
    font-size: 16px;
    line-height: 100%;
    color: var(--white-color);
    padding-left: 35px;
    display: none;

    @include tablet {
      display: block;
    }
  }
}


