  .popup {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -45%);
    z-index: 101;
    padding: 0;
    width: 100%;
    max-width: 460px;
    transition: opacity 0.5s ease;
    display: none;
  
    &.active {
      display: block;
      z-index: 100;
      transition: opacity 0.5s ease;
    }
    &__wrap {
      background-color: var(--white-color);
      display: flex;
      flex-direction: column;
      position: relative;
      padding: 49px 23px;
      border-radius: 8px;
      justify-content: center;
      min-height: 411px;
      @include tablet {
        padding: 56px 55px;
        min-height: 490px;
      }
    
    }
    &__close {
      width: 24px;
      height: 24px;
      position: absolute;
      top: 13px;
      right: 13px;
      z-index: 999;
   
        span {
          display: block;
          position: relative;
          width: 18px;
          height: 18px;
          &:before,
          &:after {
            position: absolute;
            content: " ";
            top: -2px;
            left: 9px;
            height: 24px;
            width: 2px;
            background-color: var(--gray-semi-color);
          }
          &:before {
            transform: rotate(45deg);
          }
          &:after {
            transform: rotate(-45deg);
          }
        }
    }


    &__head {
        margin-bottom: 24px;
        text-align: center;
        @include tablet {
          margin-bottom: 32px;
        }
    }

    &__title {
        font-weight: 700;
        font-size: 22px;
        line-height: 120%;
        color: var(--black-color);
        margin-bottom: 9px;

        @include tablet {
          font-size: 32px;
        }
    }

    &__subtitle {
        font-weight: 400;
        font-size: 14px;
        line-height: 120%;
        color: var(--black-color);

        @include tablet {
          font-size: 18px;
        }
    }
  
    &__links {
      display: flex;
      flex-direction: column;
      align-items: center;
      
      .popup__link {
        &:not(:last-child) {
          margin-bottom: 5px;
        }
      }
    }

    &--success {
        .popup__wrap {
            justify-content: center;
        }

        .popup__head {
            margin-bottom: 32px;
        }

        .btn--secondary {
            padding: 11px 0;
        }
    }

    &--lk {
      .popup__form {
        margin-bottom: 14px;
      }
      
      .form__input {
        padding: 6px 14px 6px 14px;

        @include tablet {
          padding: 12px 14px 12px 14px;
        }
      }
    }

    &--admission {

      .popup__wrap {
        padding: 30px 11px;

        @include tablet {
          padding: 49px 60px;
        }
      }

      .popup__item {
        margin-bottom: 24px;

        @include tablet {
          margin-bottom: 32px;
        }
      }
    }

    &--cancelorder {
      .btn--border  {
        margin-bottom: 9px;
      }
    }
  }
  