.benefits {
    background-color: var(--gray4-color);
    padding-top: 36px;
    padding-bottom: 46px;

    @include widescreen{
        padding-top: 56px;
        padding-bottom: 72px;
    }

    &__header {
        margin-bottom: 32px;
    }

    &__about {
        max-width: 861px;
        margin-bottom: 48px;

        @include tablet{
            margin-bottom: 64px;
        }

        @include widescreen{
            margin-bottom: 88px;
        }

        p {
            font-weight: 400;
            font-size: 14px;
            line-height: 135%;
            color: var(--black-color);
            
            &:not(:last-child) {
                margin-bottom: 15px;
            }
            
            @include tablet{
                font-size: 18px;
                line-height: 135%;

                &:not(:last-child) {
                    margin-bottom: 20px;
                }
                
            }
        }
    }

    &__wrapper {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-row-gap: 20px;

        @include tablet{
            grid-template-columns: repeat(6, 1fr);
            grid-column-gap: 32px;
            grid-row-gap: 24px;
        }

        @include widescreen{
            grid-template-columns: repeat(12, 1fr);
            grid-column-gap: 38px;
        }
    }

    &__item {
        grid-column: 3 span;
    }

    &__icon {
        display: none;
        margin-bottom: 12px;
        width: 70px;
        height: 70px;

        img {
            width: 100%;
            height: 100%;
        }

        @include tablet{
           display: block;
        }
    }

    &__title {
        font-weight: 700;
        font-size: 18px;
        line-height: 120%;
        color: var(--black-color);
        margin-bottom: 4px;

        @include tablet{
            font-size: 20px;
            margin-bottom: 12px;
        }
    }

    &__text {
        font-weight: 400;
        font-size: 14px;
        line-height: 135%;
        color: var(--gray-color);


        @include tablet{
            font-size: 16px;
        }

    }
}