.popular {
    padding-top: 36px;
    padding-bottom: 46px;
    background-color: var(--gray4-color);

    @include tablet{
        padding-top: 56px;
        padding-bottom: 72px;
    }

    &__wrapper {
        display: grid;

        grid-template-columns: repeat(4, 1fr);
        grid-row-gap: 20px;
        grid-column-gap: 20px;

        @include widescreen{
            grid-template-columns: repeat(12, 1fr);
            grid-row-gap: 24px;
            grid-column-gap: 40px;
        }
    }
}

.popular-item {
    display: flex;
    align-items: flex-start;
    border-radius: 4px;
    grid-column: 2 span;
    background-color: var(--white-color);
    position: relative;
    overflow: hidden;

    &:last-child {
        display: none;
    }

    padding: 12px 0px 0px 6px;
    height: 133px;

    @include tablet{
        align-items: center;
        padding: 0px 0px 0px 32px;
        border-radius: 12px;
    }

    @include widescreen{
        grid-column: 4 span;
        &:last-child {
            display: flex;
        }
    }

    &::after {
        content: "";
        position: absolute;
        top: 59px;
        right: -58px;
        z-index: 1;
        width: 164px;
        height: 175px;
        background-color: var(--green-color);
        border-radius: 50%;

        @include tablet{
            width: 254px;
            height: 254px;
            top: -15px;
            right: -125px;
        }
        
    }
    &__name {
        display: block;
        max-width: 178px;
        font-weight: 700;
        font-size: 14px;
        line-height: 110%;
        color: var(--black-color);

        @include tablet{
            font-size: 18px;
            line-height: 115%;
        }
    }

    &__icon {
        position: absolute;
        transform: translateY(-50%);
        right: 15px;
        z-index: 2;
        top: 74%;
        width: 50px;

        @include tablet{
            width: 70px;
            top: 50%;
        }
    }
}