.about {
    padding-top: 24px;
    padding-bottom: 80px;

    &-offset {
        padding-top: 120px;
    }

    @include tablet {
        padding-top: 0px;
        padding-bottom: 80px;
    }

    @include widescreen {
        padding-bottom: 120px;
    }

    &__wrapper {
     
        @include tablet {
            display: grid;
            grid-template-columns: repeat(12,1fr);
            grid-column-gap: 35px;
            grid-row-gap: 64px;
        }

        @include widescreen {
            grid-column-gap: 40px;
        }
    }

    &__subtitle {
        display: block;
        font-weight: 700;
        font-size: 18px;
        line-height: 115%;
        color: var(--black-color);
        margin-bottom: 18px;

        
        @include tablet {
            font-size: 21px;
            line-height: 120%;
        }

        @include widescreen {
            font-size: 22px;
        }
    }


    &-text {
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        color: var(--black-color);

        @include tablet {
            font-size: 18px;
            line-height: 125%;
        }
    }

    &__group {
        grid-column: 6 span;

        @include widescreen {
            grid-column: 6 span;
            grid-row: 2/2;
        }
        .btn--secondary {
            max-width: 202px;
        }
    }

    &__img {
        display: none;

        @include tablet {
            display: block;
            grid-column: 6 span;
        }

        @include widescreen {
            grid-column: 7/13;
            grid-row: 1/3;
        }
    }

    &__text {
        margin-bottom: 40px;

        @include tablet {
            grid-column: 9 span;
            margin-bottom: 0px;
        }

        @include widescreen {
            grid-column: 6 span;
            margin-bottom: 0px;
            grid-row: 1/1;
        }
    }

    &__list {
        margin-bottom: 21px;

        @include tablet {
            margin-bottom: 32px;
        }

        &-item {
            font-weight: 400;
            font-size: 14px;
            line-height: 16px;
            color: var(--black-color);
            position: relative;

            @include tablet {
                font-size: 18px;
                line-height: 115%;
            }
          
            &:not(:last-child) {
                margin-bottom: 8px;
            }

            &::before {
                content: "";
                width: 8px;
                height: 8px;
                border-radius: 100%;
                background-color: var(--green-color);
                display: inline-block;
                margin-right: 6px;

                @include tablet {
                    margin-right: 12px;
                }
            }
        }
    }

    &__wrap {
        margin-bottom: 80px;

        @include tablet {
            display: flex;
            justify-content: space-between;
            margin-bottom: 120px;
        }
    }

    &__left {
        max-width: 757px;
        margin-bottom: 40px;

        @include tablet {
            max-width: 476px;
            margin-bottom: 0px;
        }

        @include desktop {
            max-width: 757px;
        }

        .about-text {
            &:not(:last-child) {
                margin-bottom: 20px;
            }

            @include tablet {
                &:not(:last-child) {
                    margin-bottom: 32px;
                }
            }
        }
    }

    &__right {

        @include phone {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        @include tablet {
            flex-direction: column;
            justify-content: flex-start;
            max-width: 181px;
        }

        @include tablet {
            flex-direction: column;
            justify-content: space-between;
            align-items: flex-start;
            max-width: 100%;
        }
        
        &-item {
            &:not(:last-child) {
                margin-bottom: 32px;
            }

            @include phone {
                margin-bottom: 0px;
                &:not(:last-child) {
                    margin-bottom: 0px;
                }
            }

            
            @include tablet {
                &:not(:last-child) {
                    margin-bottom: 56px;
                }
            }

            @include widescreen {
                &:not(:last-child) {
                    margin-bottom: 44px;
                }
            }
        }

        &-num {
            display: block;
            font-weight: 700;
            font-size: 28px;
            line-height: 120%;
            color: var(--black-color);
          
            @include tablet {
                font-size: 40px;
            }
            
            span {
                position: relative;

                &::before {
                    content: "2";
                    position: absolute;
                    top: 0px;
                    right: -9px;
                    z-index: 1;
                    font-weight: 700;
                    font-size: 18px;
                    color: var(--black-color);
                }
            }
        }

        &-text {
            display: block;
            font-weight: 400;
         
            font-size: 16px;
            line-height: 100%;
            color: var(--black-color);

            
            @include tablet {
                font-size: 20px;
            }
        }
    }
}

.goal {
    background-color: var(--gray4-color);
    padding: 64px 0;

    @include tablet {
        padding: 96px 0;
    }

    @include widescreen {
        padding: 120px 0;
    }

    &__title {
        margin-bottom: 19px;

        @include tablet {
            margin-bottom: 24px;
        }
    }

    &__wrap {
        margin-bottom: 40px;
        max-width: 760px;

        @include tablet {
            margin-bottom: 64px;
        }

        @include widescreen {
            margin-bottom: 88px;
        }
    }

    &__text {
        &:not(:last-child) {
            margin-bottom: 21px;
        }

        @include tablet {
            &:not(:last-child) {
                margin-bottom: 31px;
            }
        }
    }

    &__wrapper {
        display: grid;
        grid-gap: 16px;
        grid-template-columns: 1fr;

        @include phone {
            grid-template-columns: repeat(4,1fr);
            grid-gap: 20px;
        }

        @include desktop {
            grid-template-columns: repeat(12,1fr);
            grid-column-gap: 32px;
        }

        @include widescreen {
            grid-template-columns: repeat(12,1fr);
            grid-column-gap: 28px;
        }
    }

    &__item {
        grid-column: 3 span;
        background-color: var(--white-color);
        border: 1px solid var(--gray-light-color);
        box-shadow: 0px 0px 20px rgba(61, 135, 189, 0.12);
        border-radius: 15px;
        padding: 12px 8px;

        @include phone {
            grid-column: 2 span;
            padding: 24px 8px;
        }

        @include tablet {
            padding: 32px 10px;
        }

        @include desktop{
            grid-column: 3 span;
        }
    }

    &__icon {
        width: 64px;
        height: 64px;
        margin-bottom: 12px;

        @include phone {
            width: 78px;
            height: 78px;
            margin-bottom: 24px;
        }

        @include tablet {
            margin-bottom: 32px;
        }
    }

    &__name {
        display: block;
        font-weight: 700;
        font-size: 18px;
        line-height: 115%;
        color: var(--black-color);
        margin-bottom: 8px;

        @include tablet {
            font-size: 22px;
            line-height: 120%;
            margin-bottom: 12px;
        }
    }

    &__descr {
        display: block;
        font-weight: 400;
        font-size: 14px;
        line-height: 120%;
        color: var(--black-color);

        @include tablet {
            font-size: 16px;
        }
    }
}

.about-offset {
    padding-top: 80px;

    @include tablet {
        padding-top: 96px;
    }

    @include desktop {
        padding-top: 120px;
        padding-bottom: 40px;
    }

    .requisites-list {
        margin-bottom: 0;
    }
}