.hero {
    padding-top: 14px;
    padding-bottom: 104px;

    @include phone{
        padding-top: 14px;
        padding-bottom: 80px;
    }

    @include tablet{
        padding-top: 32px;
        padding-bottom: 94px;
    }

    @include widescreen{
        padding-top: 32px;
        padding-bottom: 104px;
    }

    &__wrapper {
        display: grid;
        grid-template-columns: repeat(12,1fr);
        grid-gap: 14px;
    }

    &__item {
        
        &:nth-child(1) {
            grid-column: 12 span;
            grid-row: 1/3;
            position: relative;

            @include widescreen {
                grid-column: 8 span;
            }


            .swiper-horizontal > .swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction {
                bottom: -15px;
                left: 0;
                width: 100%;
            }
        }

        &:nth-child(2) {
            grid-column: 4 span;
            grid-row: 1/1;
            display: none;

            @include widescreen {
                display: block;
            }
        }

        &:nth-child(3) {
            grid-column: 4 span;
            grid-row: 2/2;
            display: none;

            @include widescreen {
                display: block;
            }
        }
    }

    &__banner {
        display: block;
      
        position: relative;
        height: 223px;

        @include phone {
            height: 347px;
        }

        @include tablet {
            height: 331px;
        }

        img {
            width: 100%;
            height: 100%;
        }

        &-wrap {
            position: absolute;
            top: 28px;
            left: 10px;
            z-index: 1;

            @include phone {
                top: 32px;
                left: 12px;
            }
    
            @include tablet {
                top: 30px;
                left: 30px;
            }
        }

        &-title {
            font-weight: 700;
           
            text-transform: uppercase;
            color: var(--white-color);
            max-width: 443px;
            margin-bottom: 8px;
            font-size: 22px;
            line-height: 120%;

            @include phone {
                max-width: 318px;
            }


            @include tablet {
                font-size: 32px;
                line-height: 120%;
                margin-bottom: 12px;
            }
        }

        &-subtitle {
            font-weight: 700;
            font-size: 16px;
            line-height: 115%;            
            color: var(--black-color);

            @include tablet {
                font-size: 18px;
                line-height: 115%;
            }
        }

        &-sm {
            height: 100%;
            overflow: hidden;
            border-radius: 12px;
            position: relative;

            img {
                height: 100%;
            }

            &__wrap {
                position: absolute;
                top: 32px;
                left: 26px;
                z-index: 1;
            }

            &__name {
                font-weight: 700;
                font-size: 18px;
                line-height: 115%;
                color: var(--black-color);
                max-width: 230px;
                margin-bottom: 37px;
            }

            &__link {
                display: inline-flex;
                align-items: center;
                font-weight: 700;
                font-size: 14px;
                line-height: 15px;
                color: var(--black-color);
                
                span {
                    height: 12px;
                    padding-left: 7px;
                }

                img {
                    width: 8px;
                }
            }
        }
    }

    &__swiper {
        overflow: hidden;
        border-radius: 8px;

        @include tablet {
            border-radius: 12px;
        }

        .swiper-button-next, .swiper-button-prev {
            position: absolute;
            top: 54%;
            width: 10px;
            height: 20px;
            opacity: 0.7;
            display: none;

            @include widescreen {
               display: block;
            }
        }

        .swiper-button-prev {
            background-image: url("../img/arrow-slider-left.svg");
            background-repeat: no-repeat;
            background-position: center;
            left: 13px;
        }

        .swiper-button-next {
            background-image: url("../img/arrow-slider-right.svg");
            background-repeat: no-repeat;
            background-position: center;
            right: 13px;
        }

        .swiper-button-next:after, .swiper-container-rtl .swiper-button-prev:after {
            content: '';
        }

        .swiper-button-prev:after, .swiper-container-rtl .swiper-button-next:after {
            content: '';
        }

    }

    &__pug {
        display: flex;
        .swiper-pagination-bullet {
            width: 120px;
            height: 4px;
            border-radius: 15px;
            background-color: transparent;
            opacity: 1;

            &::after {
               display: none;
            }
        }

        .swiper-pagination-bullet-active {
            .hero__bar {
                &::after {
                    transform: scaleX(1);
                    transition: transform var(--hero-slider-speed) ease-in-out;
                }
            }
        }
    }

    &__bar {
        position: relative;
        display: flex;
        width: 100%;
        height: 4px;
        border-radius: 15px;
        background-color: rgba($color: #B5C9CE, $alpha: 0.3);
        overflow: hidden;
        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: var(--green-color);
            border-radius: 15px;
            transform-origin: left;
            transform: scaleX(0);
           
        }
    }
}